import { Fragment, useState, useEffect } from "react";
import { axiosGet, axiosPost } from "@/services/axiosService";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import { Link } from "react-router-dom";
import environment from "@/constants/Environment";
import { setCookie } from "@/services/cookieService";
import WelcomePreview from "./WelcomePreview";
import { motion } from "framer-motion";

const WelcomePage = ({ botCustomId }) => {
  const dispatch = useDispatch();
  const [loader, setLoader] = useState<boolean>(false);
  const [blurPage, setBlurPage] = useState<boolean>(false);
  const [liveContent, setLiveContent] = useState<any>({
    showPage: true,
    showBranding: true,
    ctaText: "Get Started",
    subHeading: "Generate qualified and relevant leads",
    mainHeading: "Build your own Interactive ChatBot using Omniengage",
  });

  useEffect(() => {
    if (botCustomId) {
      axiosGet(`/users/getCompany`)
        .then((response) => {
          if (response && response.data.token) {
            setCookie(
              "token",
              response.data.token,
              1,
              environment()?.PUBLIC_DOMAIN,
              "/"
            );
            setCookie(
              "token",
              response.data.token,
              1,
              environment()?.PAGE_DOMAIN,
              "/"
            );
          }
          if (response.data.data) {
            if (
              response.data.data.subscription?.plan.trial ||
              response.data.data.omniaddons?.branding
            ) {
              setBlurPage(false);
            }
          }
        })
        .catch((err) => {
          console.log(err);
        });

      axiosGet(`/liveBotPage/getPageConfiguration?botCustomId=${botCustomId}`)
        .then((response) => {
          if (response.data.data != null) {
            setLiveContent({
              showPage: response.data.data.showPage,
              showBranding: response.data.data.showBranding,
              ctaText: response.data.data.ctaText,
              subHeading: response.data.data.subHeading,
              mainHeading: response.data.data.mainHeading,
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }, []);

  const setLivePageConfiguration = (content) => {
    const body = {
      botCustomId: botCustomId,
      ...content,
    };
    setLoader(true);
    axiosPost(`/liveBotPage/addPageConfiguration`, body)
      .then((response) => {
        setLoader(false);
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "success",
            message: "Changes Saved!",
          })
        );
      })
      .catch((err) => {
        setLoader(false);
        console.log(err);
      });
  };

  const handleContent = (field) => (event) => {
    let value;
    if (field === "showPage") value = !liveContent.showPage;
    else if (field === "showBranding") value = !liveContent.showBranding;
    else value = event.target.value;

    const updatedContent = {
      ...liveContent,
      [field]: value,
    };

    setLiveContent((prev) => ({
      ...prev,
      [field]: value,
    }));
    if (field === "showPage" || field === "showBranding")
      setLivePageConfiguration(updatedContent);
  };
  return (
    <Fragment>
      <motion.div
       initial={{opacity:0}}
       animate={{opacity:1}}
       exit={{opacity:0}}
       transition={{duration:1}}
       className="welcomepage-wrapper general-container settings-container p-fixed">
        {/* <div className="gen-header">
          <h1>Edit Welcome page</h1>
          <hr />
        </div> */}
         {blurPage && (
            <div className="disable-welcomePage p-absolute  h-100 flex-cnt">
              <div className="feature-container">
              <div className="featureicon">
                <svg
                  width="36"
                  height="42"
                  viewBox="0 0 36 42"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M30.25 14.742V12.25C30.25 9.0011 28.9594 5.88526 26.6621 3.58794C24.3647 1.29062 21.2489 0 18 0C14.7511 0 11.6353 1.29062 9.33794 3.58794C7.04062 5.88526 5.75 9.0011 5.75 12.25V14.742C4.19138 15.4222 2.86476 16.5419 1.93237 17.9641C0.999984 19.3863 0.502237 21.0494 0.5 22.75V33.25C0.502779 35.5698 1.42554 37.7938 3.06588 39.4341C4.70623 41.0745 6.93021 41.9972 9.25 42H26.75C29.0698 41.9972 31.2938 41.0745 32.9341 39.4341C34.5745 37.7938 35.4972 35.5698 35.5 33.25V22.75C35.4978 21.0494 35 19.3863 34.0676 17.9641C33.1352 16.5419 31.8086 15.4222 30.25 14.742ZM9.25 12.25C9.25 9.92936 10.1719 7.70376 11.8128 6.06282C13.4538 4.42187 15.6794 3.5 18 3.5C20.3206 3.5 22.5462 4.42187 24.1872 6.06282C25.8281 7.70376 26.75 9.92936 26.75 12.25V14H9.25V12.25ZM32 33.25C32 34.6424 31.4469 35.9777 30.4623 36.9623C29.4777 37.9469 28.1424 38.5 26.75 38.5H9.25C7.85761 38.5 6.52226 37.9469 5.53769 36.9623C4.55312 35.9777 4 34.6424 4 33.25V22.75C4 21.3576 4.55312 20.0223 5.53769 19.0377C6.52226 18.0531 7.85761 17.5 9.25 17.5H26.75C28.1424 17.5 29.4777 18.0531 30.4623 19.0377C31.4469 20.0223 32 21.3576 32 22.75V33.25Z"
                    fill="#FB5F66"
                  />
                  <path
                    d="M18 24.5C17.5359 24.5 17.0908 24.6844 16.7626 25.0126C16.4344 25.3408 16.25 25.7859 16.25 26.25V29.75C16.25 30.2141 16.4344 30.6592 16.7626 30.9874C17.0908 31.3156 17.5359 31.5 18 31.5C18.4641 31.5 18.9092 31.3156 19.2374 30.9874C19.5656 30.6592 19.75 30.2141 19.75 29.75V26.25C19.75 25.7859 19.5656 25.3408 19.2374 25.0126C18.9092 24.6844 18.4641 24.5 18 24.5Z"
                    fill="#FB5F66"
                  />
                </svg>
              </div>
              <div className="featurecontent">
                <h4>Feature locked</h4>
                <p>
                  Please upgrade to Plus/Enterprise plan and buy branding
                  removal Add-Ons to access this feature.
                </p>
                <Link
                  to="/settings"
                  onClick={() => {
                    dispatch(userSlice.actions.setSettings(6));
                  }}
                >
                  Upgrade
                </Link>
              </div>
            </div>
            </div>
          )}
        <div className="settings_wrapper fx al-fx-start jst jstfy-sp-btw gap-30">
        
          <div className="welcome-page-preview wd-50">
            <h2 className="mb-15">Preview</h2>
            <WelcomePreview liveContent={liveContent} />
          </div>
          <div className={` wd-50 ${blurPage ? "ed-disable" : " "} `}>
            <h2 className="mb-15">Edit page</h2>
            <div className="edit-page-wrapper pd-20">
              <div className="native-container p-relative">
                <div className="gen-main-content mb-10 ed-subtitle mt-0">
                  <h2>Do you want to show Welcome page ?</h2>
                </div>
                <div className="ed-toggle-outer">
                  <div
                    className="ed-toggle-row"
                    onClick={handleContent("showPage")}
                    // onClick={() => {
                    //
                    //   setShowPage(!showPage);
                    // }}
                  >
                    <div
                      className={
                        liveContent.showPage
                          ? "toggle-btn1 ed-toggle-active"
                          : "toggle-btn1"
                      }
                    >
                      Yes
                    </div>
                    <div
                      className={
                        liveContent.showPage
                          ? "toggle-btn2"
                          : "toggle-btn2 ed-toggle-active"
                      }
                    >
                      No
                    </div>
                  </div>
                </div>
              </div>

              <div
                className={
                  liveContent.showPage
                    ? "ed-container"
                    : "ed-container ed-disable"
                }
              >
                <div className="ed-title ed-subtitle mb-15 ">
                  <h2>Edit page contents</h2>
                </div>
                <div className="ed-row">
                  <div className="ed-col w-100">
                    <label htmlFor="">Edit main heading</label>
                    <input
                      type="text"
                      placeholder="Build your own customized Workflow using Omniengage."
                      value={liveContent.mainHeading}
                      // onChange={(e: any) => setMainHeading(e.target.value)}
                      onChange={handleContent("mainHeading")}
                      onBlur={() => setLivePageConfiguration(liveContent)}
                      disabled={!liveContent.showPage}
                    />
                  </div>
                  <div className="ed-col w-100">
                    <label htmlFor="">Edit subheading</label>
                    <input
                      type="text"
                      placeholder="Generate qualified and relevant leads "
                      value={liveContent.subHeading}
                      // onChange={(e: any) => setSubHeading(e.target.value)}
                      onChange={handleContent("subHeading")}
                      onBlur={() => setLivePageConfiguration(liveContent)}
                      disabled={!liveContent.showPage}
                    />
                  </div>
                  <div className="ed-col w-100 p-relative">
                    <label htmlFor="">Edit CTA</label>
                    <input
                      type="text"
                      placeholder="Get Started"
                      value={liveContent.ctaText}
                      // onChange={(e: any) => setCtaText(e.target.value)}
                      onChange={handleContent("ctaText")}
                      onBlur={() => setLivePageConfiguration(liveContent)}
                      disabled={!liveContent.showPage}
                    />
                  </div>
                </div>

                <div className="ed-checkbox">
                  <div className="ed-checkbox-title">
                    <p>Do you want to show Ominengage branding ? </p>
                  </div>
                  <div className="ed-toggle-outer">
                    <div
                      className="ed-toggle-row"
                      onClick={handleContent("showBranding")}
                    >
                      <div
                        className={
                          liveContent.showBranding
                            ? "toggle-btn1 ed-toggle-active"
                            : "toggle-btn1"
                        }
                      >
                        Yes
                      </div>
                      <div
                        className={
                          liveContent.showBranding
                            ? "toggle-btn2 "
                            : "toggle-btn2 ed-toggle-active"
                        }
                      >
                        No
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            {/* <div className="edsave-btn">
            <button disabled={blurPage ? true : false}>
              {loader ? (
                <div className="fx al-fx-end jstfy-cnt gap-6">
                  Saving <ThreeDot />
                </div>
              ) : (
                "Save Changes"
              )}
            </button>
          </div> */}
          </div>
        </div>
      </motion.div>
    </Fragment>
  );
};

export default WelcomePage;
