import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { userSlice } from "@/redux/User";
import { State } from "@/redux/Types";
import environment from "@/constants/Environment";

const environmentData = environment();

export default function CopyButton() {
  const { botUrl } = useParams();
  const dispatch = useDispatch();
  const subDomain = useSelector(
    (state: State) => state.user.companyDetails.subDomain,
  );

  function handleCopy() {
    try {
      const { ENVIRONMENT, LIVEBOT_URL } = environmentData;
      const origin =
        ENVIRONMENT === "production"
          ? LIVEBOT_URL.replace("<subDomain>", subDomain)
          : LIVEBOT_URL;

      const liveBotUrl = `${origin}/${botUrl}`;
      navigator.clipboard.writeText(liveBotUrl);

      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "success",
          message: "Copied to clipboard!",
        }),
      );
    } catch (err) {
      console.log(err);
    }
  }

  return (
    <div className="copy-builder p-relative cp">
      <div className="btn-copy" onClick={handleCopy}>
        <svg
          width={16}
          height={18}
          viewBox="0 0 16 18"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M8.75 15C9.7442 14.9988 10.6973 14.6034 11.4003 13.9003C12.1033 13.1973 12.4988 12.2442 12.5 11.25V4.68226C12.5012 4.28803 12.4241 3.89749 12.2732 3.53328C12.1223 3.16906 11.9006 2.83842 11.621 2.56051L9.9395 0.879013C9.66159 0.599393 9.33095 0.377715 8.96674 0.226828C8.60252 0.0759412 8.21198 -0.00115346 7.81775 1.30422e-05H4.25C3.2558 0.00120394 2.30267 0.396675 1.59966 1.09968C0.896661 1.80268 0.501191 2.75582 0.5 3.75001V11.25C0.501191 12.2442 0.896661 13.1973 1.59966 13.9003C2.30267 14.6034 3.2558 14.9988 4.25 15H8.75ZM2 11.25V3.75001C2 3.15328 2.23705 2.58098 2.65901 2.15902C3.08097 1.73707 3.65326 1.50001 4.25 1.50001C4.25 1.50001 7.93925 1.51051 8 1.51801V3.00001C8 3.39784 8.15804 3.77937 8.43934 4.06067C8.72064 4.34198 9.10218 4.50001 9.5 4.50001H10.982C10.9895 4.56076 11 11.25 11 11.25C11 11.8468 10.7629 12.419 10.341 12.841C9.91903 13.263 9.34674 13.5 8.75 13.5H4.25C3.65326 13.5 3.08097 13.263 2.65901 12.841C2.23705 12.419 2 11.8468 2 11.25ZM15.5 6.00001V14.25C15.4988 15.2442 15.1033 16.1973 14.4003 16.9003C13.6973 17.6034 12.7442 17.9988 11.75 18H5C4.80109 18 4.61032 17.921 4.46967 17.7803C4.32902 17.6397 4.25 17.4489 4.25 17.25C4.25 17.0511 4.32902 16.8603 4.46967 16.7197C4.61032 16.579 4.80109 16.5 5 16.5H11.75C12.3467 16.5 12.919 16.263 13.341 15.841C13.7629 15.419 14 14.8468 14 14.25V6.00001C14 5.8011 14.079 5.61034 14.2197 5.46968C14.3603 5.32903 14.5511 5.25001 14.75 5.25001C14.9489 5.25001 15.1397 5.32903 15.2803 5.46968C15.421 5.61034 15.5 5.8011 15.5 6.00001Z"
            fill="#FB5F66"
          />
        </svg>
        <div className="preview-tp">
          <span className="tooltip-text">Copy link to clipboard</span>
        </div>
      </div>
    </div>
  );
}
