import { useMemo, useEffect, useState, useCallback } from "react";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { userSlice } from "@/redux/User";
import { State } from "@/redux/Types";
import environment from "@/constants/Environment";
import useStore from "@/pages/omni-builder/store";
import { axiosPost } from "@/services/axiosService";
import useWhatsAppConfig from "@/pages/omni-builder/react-query/configs/useWhatsAppConfig";
import { useQueryClient } from "@tanstack/react-query";
import _ from "lodash";
const environmentData = environment();

interface Props {
  onClose: () => void;
  metaData: any;
}

export default function PublishModal({ onClose, metaData }: Props) {
  const { botUrl } = useParams();
  const dispatch = useDispatch();
  const subDomain = useSelector(
    (state: State) => state.user.companyDetails.subDomain,
  );
  const setOpenConfigModalId = useStore((state) => state.setOpenConfigModalId);
  const isWhatsappLoading = useStore((state) => state.isWhatsappLoading);
  const [isToggleOn, setIsToggleOn] = useState(false);
  const [openDropdownId, setOpenDropdownId] = useState<string>("");
  const [phoneNumbers, setPhoneNumbers] = useState<any[]>([]);
  const [selectedPhoneNumber, setSelectedPhoneNumber] = useState<any>({
    display_phone_number: "Select a Phone Number",
  });
  const [triggerMessages, setTriggerMessages] = useState<any[]>([]);
  const [triggerVal, setTriggerVal] = useState<string>("");
  const [whatsappConnection, setWhatsappConnection] = useState<any>();
  const whatsappConfiguration = useWhatsAppConfig();
  const queryClient = useQueryClient();

  useEffect(() => {
    if (whatsappConfiguration.isLoading || whatsappConfiguration.isFetching)
      return;

    if (whatsappConfiguration.isError) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          // message: whatsappConfiguration.error.message,
          message: "Error while fetching whatsapp configuration",
        }),
      );
      onClose();
      return;
    }

    const config: any = whatsappConfiguration.data;

    setWhatsappData(config);
  }, [
    whatsappConfiguration.data,
    whatsappConfiguration.isError,
    whatsappConfiguration.isFetching,
  ]);

  const setWhatsappData = (config: any) => {
    const phoneNumbers = config?.phoneNumbers;
    setWhatsappConnection(config); //complete data
    setPhoneNumbers(phoneNumbers ?? []);

    config?.connections?.forEach((conn: any) => {
      conn.configs.find((c: any) => {
        if (c.botCustomId === metaData.botCustomId) {
          setIsToggleOn(true);
          setTriggerMessages(c.triggers);
          setSelectedPhoneNumber(
            phoneNumbers.find((e: any) => e.id === conn.phoneNumberId),
          );
        }
      });
    });
  };

  const handleToggle = () => {
    if (isToggleOn) {
      removeWhatsappBotConnection();
    }
    setIsToggleOn(!isToggleOn);
  };

  function handleDropdown(id: string) {
    setOpenDropdownId(openDropdownId ? "" : id);
  }

  const updateWhatsappConnection = async (
    phoneNumberId: string,
    updatedTriggers: any = "",
  ) => {
    const oldTriggers =
      whatsappConnection?.connections?.find(
        (conn: any) => conn.phoneNumberId === phoneNumberId,
      )?.configs || [];

    const toSend = {
      phoneNumberId,
      configs: [
        ...oldTriggers.filter(
          (config: any) => config?.botCustomId !== metaData?.botCustomId,
        ),
        {
          botCustomId: metaData?.botCustomId,
          triggers: updatedTriggers ? updatedTriggers : triggerMessages,
        },
      ],
    };

    const response: any = await axiosPost(
      `/users/assignBotsToWhatsappNumber`,
      toSend,
    );

    if (!response.data.success) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: response.data.message || "Something went wrong",
        }),
      );
    }

    queryClient.invalidateQueries({
      queryKey: ["configurations", "whatsapp"],
    });
    const config = response?.data?.data;
    const temp = {};
    config?.connections?.forEach((e: any) => {
      temp[e.phoneNumberId] = e.configs.map(
        (config: any) => config.botCustomId,
      );
    });

    setSelectedPhoneNumber(
      phoneNumbers.find((phone: any) => phone.id === phoneNumberId),
    );
  };

  const removeWhatsappBotConnection = async () => {
    const toSend = {
      phoneNumberId: selectedPhoneNumber.id,
      botCustomId: metaData.botCustomId,
    };

    setSelectedPhoneNumber({
      display_phone_number: "Select a Phone Number",
    });
    setTriggerMessages([]);
    setTriggerVal("");
    await axiosPost(
      `/users/assignBotsToWhatsappNumber?disconnect=${true}`,
      toSend,
    );

    queryClient.invalidateQueries({
      queryKey: ["configurations", "whatsapp"],
    });
  };

  const { liveBotUrl, embedScript } = useMemo(() => {
    const { ENVIRONMENT, LIVEBOT_URL } = environmentData;
    const origin =
      ENVIRONMENT === "production"
        ? LIVEBOT_URL.replace("<subDomain>", subDomain)
        : LIVEBOT_URL;

    const liveBotUrl = `${origin}/${botUrl}`;
    const embedScript = `<script type="text/javascript">window.LIVE_URL="${liveBotUrl}?embed=true",d=document,s=d.createElement("script"),s.src="${origin}/embed.js",s.defer=1,d.getElementsByTagName("head")[0].appendChild(s)</script>`;

    return { liveBotUrl, embedScript };
  }, [botUrl, subDomain]);

  function handleCopy(value: string) {
    try {
      navigator.clipboard.writeText(value);

      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "success",
          message: "Copied to clipboard!",
        }),
      );
    } catch (err) {
      console.log(err);
    }
  }

  const handleTriggerDelete = (msg: string) => {
    const updatedData = triggerMessages.filter((item) => item != msg);
    setTriggerMessages(updatedData);
    updateWhatsappConnection(selectedPhoneNumber?.id, updatedData);
  };

  return (
    <>
      {environment().isAI ? (
        <div className="createbot-modal go-live-popup ">
          <div
            className="go-live-inner-popup"
            onClick={(e) => e.stopPropagation()}
          >
            <div className="close flex-cnt" onClick={onClose}>
              <svg
                width="10"
                height="10"
                viewBox="0 0 10 10"
                fill="#6D6D6D"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                  fill="#6D6D6D"
                />
              </svg>
            </div>
            <div className="go-live-inner-header">
              <h2>Bot share link</h2>
            </div>
            <div className="go-live-inner-body">
              <div className="link-body">
                <div className="info-dummy">
                  <span>Note:</span>
                  <p>
                    For the best user experience, we recommend using a device
                    with a screen resolution of 1280X960 pixels or higher when
                    accessing the builder. This way, you can fully enjoy all the
                    features and tools available to create your masterpiece.
                  </p>
                </div>
                <h4>Share Link:</h4>
                <p>
                  Share your bot with ease using the Share Link section and
                  reach your audience in no time!
                </p>
                <div className="link-text">
                  <span>
                    <input type="text" readOnly value={liveBotUrl} />
                  </span>
                  <button onClick={() => handleCopy(liveBotUrl)}>Copy</button>
                </div>
              </div>
              <div className="link-body">
                <h4>Embed Link:</h4>
                <p>
                  Easily embed your bot on your website using the Embed Code.
                </p>
                <div className="link-text">
                  <span>
                    <input type="text" value={embedScript} readOnly />
                  </span>
                  <button onClick={() => handleCopy(embedScript)}>Copy</button>
                </div>
              </div>
            </div>
            <div className="go-live-inner-footer">
              <button onClick={onClose}>Done</button>
            </div>
          </div>
        </div>
      ) : (
        <div
          className="createbot-modal go-live-popup publish_modal"
          onClick={onClose}
        >
          <div
            className="publish_settings p-absolute  fx fx-d-col al-cnt"
            onClick={(e) => e.stopPropagation()}
          >
            <div className="publish_head fx al-cnt fx-d-col w-100">
              <h2>Publish Settings</h2>
              <p className="mt-8">Live your bot on different platforms</p>
              <div className="close new-modal-close flex-cnt" onClick={onClose}>
                <svg
                  width="10"
                  height="10"
                  viewBox="0 0 10 10"
                  fill="#6D6D6D"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                    fill="#6D6D6D"
                  />
                </svg>
              </div>
            </div>
            <div className="publish_body">
              {/* <div className="social_platforms">
                <h3 className="mb-8">Social Platform</h3>
                <div className="social_app fx al-cnt jstfy-sp-btw fx-wrap mb-10">
                  <div className="fx al-cnt app-details">
                    <div className="icon grid-cnt">
                      <FacebookIconSvg />
                    </div>
                    <div className="fb-info">
                      <div className="text">
                        <h4>Facebook</h4>
                        <p>Setup your Facebook Page to start bot</p>
                      </div>
                    </div>
                  </div>
                  <div className="toggle-btn">
                    <label className="switch">
                      <input type="checkbox" />
                      <span className="toggle round"></span>
                    </label>
                  </div>
                  <div className={`app-input w-100`}>
                    <input placeholder="reandom text" />
                  </div>
                </div>
                <div className="social_app fx al-cnt jstfy-sp-btw fx-wrap">
                  <div className="fx al-cnt app-details">
                    <div className="icon grid-cnt">
                      <WhatsappIconSvg />
                    </div>
                    <div className="fb-info">
                      <div className="text">
                        <h4>Whatsapp</h4>
                        <p>Setup your whatsapp number to start bot</p>
                      </div>
                    </div>
                  </div>
                  {isWhatsappLoading ? (
                    <div className="load-container">
                      <div className="linespinner"></div>
                    </div>
                  ) : (
                    <>
                      {whatsappConfiguration?.data ? (
                        <div className="toggle-btn">
                          <label className="switch">
                            <input
                              type="checkbox"
                              checked={isToggleOn}
                              onChange={handleToggle}
                            />
                            <span className="toggle round"></span>
                          </label>
                        </div>
                      ) : (
                        <span
                          className="connect_btn cp"
                          onClick={() => {
                            setOpenConfigModalId("whatsapp");
                          }}
                        >
                          Connect +{" "}
                        </span>
                      )}
                    </>
                  )}

                  {!whatsappConfiguration?.data || isWhatsappLoading ? null : (
                    <div
                      className={`app-input  w-100 pd-t-30 ${
                        isToggleOn ? "visible" : ""
                      }`}
                    >
                      <span className="data-box-label">Select number</span>
                      <div
                        className="node-dropdown variableTypeDropDown rightSidebar-tarea no-mt p-relative w-50"
                        onClick={() => handleDropdown("phone")}
                        onMouseLeave={() => handleDropdown("")}
                      >
                        <span>{selectedPhoneNumber?.display_phone_number}</span>
                        <svg
                          width="10"
                          height="6"
                          viewBox="0 0 10 6"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            d="M1 1L5 5L9 1"
                            stroke="#444444"
                            strokeWidth="2"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                          />
                        </svg>
                        {openDropdownId === "phone" && (
                          <div className="dropdown-items w-100 full-view-menu new-scrollbar ">
                            <ul className="grey-scrollbar fx fx-d-col gap-5">
                              {phoneNumbers.length === 0 ? (
                                <span>No numbers found</span>
                              ) : (
                                phoneNumbers.map((phoneNumber) => (
                                  <li
                                    onClick={() => {
                                      updateWhatsappConnection(phoneNumber?.id);
                                    }}
                                    key={phoneNumber?.id}
                                    className={
                                      selectedPhoneNumber?.id ===
                                      phoneNumber?.id
                                        ? "active-item"
                                        : ""
                                    }
                                  >
                                    {phoneNumber?.display_phone_number}
                                  </li>
                                ))
                              )}
                            </ul>
                          </div>
                        )}
                      </div>
                      {selectedPhoneNumber?.id && (
                        <>
                          <label className="data-box-label mt-15 mb-10">
                            Enter trigger keywords
                          </label>
                          <div className="fx fx-wrap publish_input_wrapper publish_input gap-5">
                            {triggerMessages.map((triggerMessage, index) => {
                              return (
                                <div
                                  className="fx al-cnt w-mc trigger_msg gap-8"
                                  key={index}
                                >
                                  <span>{triggerMessage}</span>
                                  <span
                                    className="cp grid"
                                    onClick={() =>
                                      handleTriggerDelete(triggerMessage)
                                    }
                                  >
                                    <CrossIconSvg />
                                  </span>
                                </div>
                              );
                            })}
                            <input
                              placeholder="enter keyword"
                              value={triggerVal}
                              onChange={(e) => setTriggerVal(e.target.value)}
                              onKeyDown={(e: any) => {
                                if (e.key === "Enter") {
                                  e.preventDefault();
                                  const tVal = e.target.value.trim();
                                  if (tVal === "") return;

                                  setTriggerVal("");
                                  updateWhatsappConnection(
                                    selectedPhoneNumber?.id,
                                    [...triggerMessages, tVal],
                                  );

                                  setTriggerMessages((prevValues) => {
                                    return [...prevValues, tVal];
                                  });
                                }
                              }}
                            />
                          </div>
                        </>
                      )}
                    </div>
                  )}
                </div>
              </div> */}
              <div>
                {/* {className="web_opts"} */}
                <h3 className="mb-8">Web Options</h3>
                <div className="social_app fx jstfy-sp-btw al-cnt">
                  <div className="fx al-cnt app-details">
                    <div className="icon grid-cnt">
                      <WebBotIconSvg />
                    </div>
                    <div className="fb-info">
                      <div className="text">
                        <h4>Web bot</h4>
                        <p>Copy Your web bot link and use anywhere</p>
                      </div>
                    </div>
                  </div>
                  <div
                    className="copy_icon grid-cnt cp"
                    onClick={() => handleCopy(liveBotUrl)}
                  >
                    <CopyIconSvg />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
}

