import { useState } from "react";
import ThreeDot from "../loaders/ThreeDot";
import { PublishMode } from "@/types/builder";
import axios from "axios";
import { axiosGet, axiosPost } from "@/services/axiosService";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";

interface Props {
  metaData: Record<string, any>;
  loader: boolean;
  onClick: (enableConfetti: boolean) => void;
}

export default function PublishButton({ metaData, loader, onClick }: Props) {
  const lastPublishTime = metaData.lastPublishTime;
  const updatedAt = new Date(metaData.updatedAt).getTime();

  const mode: PublishMode =
    metaData.botType == "campaign"
      ? "campaign"
      : lastPublishTime
      ? updatedAt > lastPublishTime
        ? "update"
        : "live"
      : "new";

  const handleClick = () => {
    if (mode === "new") {
      return onClick(true);
    }

    return onClick(false);
  };

  return (
    <>
      <div
        className="publish-cta valid publish__desktop cp"
        onClick={handleClick}
      >
        {!loader && <PublishIcon />}
        <p>{loader ? "Publishing..." : BUTTON_TEXT[mode]}</p>
      </div>
      <div className="publish-cta publish__mobile" onClick={handleClick}>
        {loader ? <ThreeDot /> : <PublishIcon />}
      </div>
    </>
  );
}

const BUTTON_TEXT: Record<PublishMode, string> = {
  live: "Published",
  update: "Publish Changes",
  new: "Go Live",
  campaign: "Send Campaign",
};

