import { Fragment, useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { userSlice } from "@/redux/User";
import { State } from "@/redux/Types";
import { io } from "socket.io-client";
import { axiosGet } from "@/services/axiosService";
import ExitConditionSetupModal from "./ExitConditionSetupModal";
import environment from "@/constants/Environment";
import flatObject from "@/utils/flatObject";
import type { Variables } from "@/pages/omni-builder/types";
import { responseToVariables } from "./ExitCondition";
import "@/styles/downgrade.css";
import { WEBHOOK_SUPPORT_DOC } from "@/constants/URLS";

const { SERVER_API_URL } = environment();

interface Props {
  webhookData: Record<string, any>;
  conditionData: Record<string, any>;
  nodeVars: Variables;
  botCustomId: string;
  onDataChange: (field: string, value: any) => void;
  onClose: () => void;
}

export default function ExitWebhookModal({
  webhookData,
  conditionData,
  nodeVars,
  botCustomId,
  onDataChange,
  onClose,
}: Props) {
  const dispatch = useDispatch();
  const userId = useSelector((state: State) => state.user.userId);
  const [apiKey, setApiKey] = useState();
  const [stepNumber, setStepNumber] = useState<number>(1);
  const [responseVars, setResponseVars] = useState<Variables>(
    responseToVariables(webhookData.variables, "webhook")
  );

  const webhookUrl = `${SERVER_API_URL}/users/exitWebhook?id=${botCustomId}&api_key=${apiKey}`;

  useEffect(() => {
    fetchApiKey();

    const socket = io(SERVER_API_URL, {
      transports: ["websocket"],
    });

    socket.emit("join", {
      type: "exit",
      userId,
      botCustomId,
    });

    socket.on("outgrowData", (response) => {
      const flatResponse = flatObject(response.data);
      const responseVars = responseToVariables(flatResponse, "webhook");
      setResponseVars(responseVars);
      onDataChange("variables", flatResponse);
      setStepNumber(2);
    });

    return () => {
      socket.disconnect();
    };
  }, []);

  async function fetchApiKey() {
    try {
      const response = await axiosGet("/api/getApiKey");
      setApiKey(response.data.api_key);
    } catch (error: any) {
      console.log(error);
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: error.response.data.message || "Something went wrong",
        })
      );
    }
  }

  async function generateApiKey() {
    try {
      const response = await axiosGet("/api/genApiKey");
      setApiKey(response.data.data.apiKey);
    } catch (error: any) {
      console.log(error);
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: error.response.data.message || "Something went wrong",
        })
      );
    }
  }

  async function handleCopy() {
    await navigator.clipboard.writeText(webhookUrl);
    dispatch(
      userSlice.actions.setToastPopUp({
        show: true,
        toastType: "success",
        message: "Copied to Clipboard!",
      })
    );
  }

  return (
    <div className="createbot-modal">
      <div className="call-api-container call-api">
        <div className="call-api-close close" onClick={onClose}>
          <svg
            width="10"
            height="10"
            viewBox="0 0 10 10"
            fill="#6D6D6D"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
              fill="#6D6D6D"
            />
          </svg>
        </div>
        <div className="call-api-header pd-30" style={{ height: "unset" }}>
          <h2>Setup exit conditions</h2>
          <div className="downgrade-progress flex-cnt">
            {STEPS.map((step, stepIndex: number) => (
              <Fragment key={stepIndex}>
                <div
                  className="downgrade-progress-step fx al-cnt"
                  onClick={() => setStepNumber(stepIndex + 1)}
                >
                  {stepIndex < stepNumber ? (
                    <div className="downgrade-progress-icon p-relative flex-cnt">
                      <span>{stepIndex + 1}</span>
                      <svg
                        width="24"
                        height="24"
                        viewBox="0 0 24 24"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <circle
                          cx="12"
                          cy="12"
                          r="11.5"
                          fill="#FB5F66"
                          stroke="#FB5F66"
                        />
                        <circle cx="12" cy="12" r="10" fill="#FF8287" />
                      </svg>
                    </div>
                  ) : (
                    <div className="downgrade-progress-icon p-relative flex-cnt">
                      <span className="inactive">{stepIndex + 1}</span>
                      <svg
                        width="24"
                        height="24"
                        viewBox="0 0 24 24"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <circle
                          cx="12"
                          cy="12"
                          r="11.5"
                          stroke="#999999"
                          strokeLinejoin="round"
                          strokeDasharray="3 3"
                        />
                        <circle cx="12" cy="12" r="9" fill="#F1F1F1" />
                      </svg>
                    </div>
                  )}
                  <p
                    style={{
                      color: `${
                        stepIndex < stepNumber ? "#444444" : "#999999"
                      }`,
                      fontSize: "14px",
                    }}
                  >
                    {step}
                  </p>
                </div>
                {stepIndex + 1 < STEPS.length && (
                  <svg
                    width="6"
                    height="10"
                    viewBox="0 0 6 10"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M0.532695 0.627237C0.532841 0.792941 0.598784 0.951806 0.716028 1.0689L3.9127 4.26557C4.00945 4.3623 4.08621 4.47715 4.13857 4.60355C4.19094 4.72994 4.21789 4.86542 4.21789 5.00224C4.21789 5.13905 4.19094 5.27453 4.13857 5.40093C4.08621 5.52733 4.00945 5.64217 3.9127 5.7389L0.720195 8.9314C0.606346 9.04928 0.543349 9.20716 0.544774 9.37103C0.546197 9.5349 0.611928 9.69166 0.727808 9.80754C0.843688 9.92342 1.00045 9.98915 1.16432 9.99057C1.32819 9.992 1.48607 9.929 1.60394 9.81515L4.79645 6.62515C5.22571 6.19504 5.4668 5.6122 5.4668 5.00453C5.4668 4.39686 5.22571 3.81401 4.79645 3.3839L1.59978 0.185153C1.51237 0.0976884 1.40098 0.0381173 1.2797 0.0139781C1.15843 -0.0101611 1.03272 0.00221645 0.918479 0.0495446C0.804241 0.0968727 0.706608 0.177024 0.637936 0.279857C0.569264 0.38269 0.532639 0.503582 0.532695 0.627237Z"
                      fill="#999999"
                    />
                  </svg>
                )}
              </Fragment>
            ))}
          </div>
        </div>
        {stepNumber === 1 && (
          <div className="call-api-content">
            <div className="webhook-data-box-container">
              <div className="data-box-label">
                <span className="webhook_head">support doc</span>
              </div>
              <div className="data-box-wrap mb-20">
                <div className="view-support-doc">
                  <p>
                    You can understand how to use this node by opening the
                    support document.
                  </p>
                  <a
                    href={WEBHOOK_SUPPORT_DOC}
                    target="_blank"
                    className="cursor-pointer"
                  >
                    <svg
                      width="12"
                      height="12"
                      viewBox="0 0 12 12"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path                      
                        d="M9.33333 11.9993H2C1.46957 11.9993 0.960859 11.7886 0.585787 11.4136C0.210714 11.0385 0 10.5298 0 9.99935V2.66602C0 2.13558 0.210714 1.62687 0.585787 1.2518C0.960859 0.876729 1.46957 0.666016 2 0.666016H4.66667C4.84348 0.666016 5.01305 0.736254 5.13807 0.861278C5.2631 0.986302 5.33333 1.15587 5.33333 1.33268C5.33333 1.50949 5.2631 1.67906 5.13807 1.80409C5.01305 1.92911 4.84348 1.99935 4.66667 1.99935H2C1.82319 1.99935 1.65362 2.06959 1.5286 2.19461C1.40357 2.31964 1.33333 2.4892 1.33333 2.66602V9.99935C1.33333 10.1762 1.40357 10.3457 1.5286 10.4708C1.65362 10.5958 1.82319 10.666 2 10.666H9.33333C9.51014 10.666 9.67971 10.5958 9.80474 10.4708C9.92976 10.3457 10 10.1762 10 9.99935V7.33268C10 7.15587 10.0702 6.9863 10.1953 6.86128C10.3203 6.73625 10.4899 6.66602 10.6667 6.66602C10.8435 6.66602 11.013 6.73625 11.1381 6.86128C11.2631 6.9863 11.3333 7.15587 11.3333 7.33268V9.99935C11.3333 10.5298 11.1226 11.0385 10.7475 11.4136C10.3725 11.7886 9.86377 11.9993 9.33333 11.9993Z"
                        fill="#FB5F66"
                      />
                      <path
                        d="M11.332 0H7.33199C7.15518 0 6.98561 0.0702379 6.86059 0.195262C6.73556 0.320286 6.66532 0.489856 6.66532 0.666667C6.66532 0.843478 6.73556 1.01305 6.86059 1.13807C6.98561 1.2631 7.15518 1.33333 7.33199 1.33333H9.72532L3.52532 7.52667C3.46284 7.58864 3.41324 7.66238 3.3794 7.74362C3.34555 7.82486 3.32812 7.91199 3.32812 8C3.32812 8.08801 3.34555 8.17515 3.3794 8.25638C3.41324 8.33762 3.46284 8.41136 3.52532 8.47333C3.5873 8.53582 3.66103 8.58541 3.74227 8.61926C3.82351 8.65311 3.91065 8.67053 3.99866 8.67053C4.08667 8.67053 4.1738 8.65311 4.25504 8.61926C4.33628 8.58541 4.41002 8.53582 4.47199 8.47333L10.6653 2.27333V4.66667C10.6653 4.84348 10.7356 5.01305 10.8606 5.13807C10.9856 5.2631 11.1552 5.33333 11.332 5.33333C11.5088 5.33333 11.6784 5.2631 11.8034 5.13807C11.9284 5.01305 11.9987 4.84348 11.9987 4.66667V0.666667C11.9987 0.489856 11.9284 0.320286 11.8034 0.195262C11.6784 0.0702379 11.5088 0 11.332 0Z"
                        fill="#FB5F66"
                      />
                    </svg>
                    View support doc
                  </a>
                </div>
              </div>
            </div>
            <div className="webhook-data-box-container">
              <div className="data-box-label">
                <span className="webhook_head">Webhook endpoint</span>
              </div>
              <div className="data-box-wrap mb-20">
                <div className="row-container">
                  <span className="input-label wh-bx-title">
                    Send data to Omniengage
                    <span
                      className="cursor-pointer color-red"
                      onClick={handleCopy}
                    >
                      Copy
                    </span>
                  </span>
                </div>
                <div className="copy-url-btns">
                  <input
                    className="rightSidebar-tarea wh-input"
                    type="text"
                    value={webhookUrl}
                    readOnly
                  />
                </div>
              </div>
            </div>
          </div>
        )}
        {stepNumber === 2 && (
          <ExitConditionSetupModal
            conditionData={conditionData}
            nodeVars={nodeVars}
            responseVars={responseVars}
            onDataChange={(conditionData) =>
              onDataChange("conditionData", conditionData)
            }
          />
        )}
        <div className="call-api-footer">
          {apiKey ? (
            <button className="save-btn" onClick={onClose}>
              Save
            </button>
          ) : (
            <button className="save-btn" onClick={generateApiKey}>
              Generate API Key"
            </button>
          )}
        </div>
      </div>
    </div>
  );
}

const STEPS =
  window.innerWidth < 990
    ? ["Configure Webhook", "Setup conditions"]
    : (["Configure Webhook", "Setup conditions"] as const);
