import { Fragment, useEffect, useMemo, useRef, useState } from "react";
import environment from "@/constants/Environment";
import {
  ChatIcon_1,
  ChatIcon_10,
  ChatIcon_11,
  ChatIcon_2,
  ChatIcon_3,
  ChatIcon_4,
  ChatIcon_5,
  ChatIcon_6,
  ChatIcon_7,
  ChatIcon_8,
  ChatIcon_9,
  Chat_Head_Close,
  Desktop_Icon,
  Mobile_Icon,
  Tablet_Icon,
} from "@/assets/AiBotAssets";
import { ChromePicker } from "react-color";
import Preview from "@/pages/preview/Preview";
import { Bot, State } from "@/redux/Types";
import { useSelector } from "react-redux";
import * as filestack from "filestack-js";
const apikey = environment().FILESTACK_API_KEY;
const client = filestack.init(apikey);

const botIcons = [
  {
    id: 1,
    icon: <ChatIcon_1 />,
  },
  {
    id: 2,
    icon: <ChatIcon_2 />,
  },
  {
    id: 3,
    icon: <ChatIcon_7 />,
  },
  {
    id: 4,
    icon: <ChatIcon_8 />,
  },
  {
    id: 5,
    icon: <ChatIcon_9 />,
  },
  {
    id: 6,
    icon: <ChatIcon_5 />,
  },
  {
    id: 7,
    icon: <ChatIcon_6 />,
  },
  {
    id: 8,
    icon: <ChatIcon_3 />,
  },
  {
    id: 9,
    icon: <ChatIcon_4 />,
  },
  {
    id: 10,
    icon: <ChatIcon_10 />,
  },
  {
    id: 11,
    icon: <ChatIcon_11 />,
  },
];

const tabItems = [
  {
    tabName: "Desktop",
    icon: <Desktop_Icon />,
  },
  {
    tabName: "Tablet",
    icon: <Tablet_Icon />,
  },
  {
    tabName: "Mobile",
    icon: <Mobile_Icon />,
  },
];

const units = ["minutes", "seconds"];

const initialState = {
  selectedDisplayOption: "customPopup",
  popupCustomText: "Got any questions? I’m happy to help.",
  iconIndex: 5,
  iconBackground: "#fb5f66",
  iconColor: "#ffe2e2",
  iconURl: "",
  widthInpageValue: "",
  heightInpageValue: "",
  widthChatbotValue: "",
  heightChatbotValue: "",
  widthInpageUnit: "px",
  heightInpageUnit: "px",
  widthChatbotUnit: "px",
  heightChatbotUnit: "px",
  triggerState: null,
  triggerName: "",
  extraDetails: {},
};
const initialShowStates = {
  displayAppearance: 1,
  iconColorPicker: false,
  backgroundColorPicker: false,
  widthInpage: false,
  heightInpage: false,
  widthChatbot: false,
  heightChatbot: false,
  chatbotPopup: true,
  popUpMessage: true,
};
const toggleStates = {
  customEmbedInPage: false,
  customEmbedChatbot: false,
  chatDisplay: false,
};
interface Props {
  metaData: Record<string, any>;
  onDataChange(field: string, value: any): void;
}

const environmentData = environment();

export default function EmbedWebpage({ metaData, onDataChange }: Props) {
  const {
    _id: botId,
    projectName: botName,
    embedConfig,
    chatConfig,
  } = metaData;
  const [showtooptip, setShowTooltip] = useState<any>(false);
  const [show, setShow] = useState(initialShowStates);
  const [embedStyle, setEmbedStyle] = useState("chatstyle");
  const [chatPopup, setChatPopup] = useState<Boolean>(false);
  const subDomain = useSelector(
    (state: State) => state.user.companyDetails.subDomain,
  );
  const inPageCustomEmbed = embedConfig.inPageCustomEmbed;

  const handleShow = (
    type: string,
    data: any,
    type1: any = null,
    data1: any = null,
  ) => {
    // console.log(type, data);
    if (type1 && data1) {
      setShow({ ...show, [type]: data, [type1]: data1 });
    } else {
      setShow({ ...show, [type]: data });
    }
  };

  const uploadCustomIcon = async () => {
    var options = {
      accept: ["image/*", "video/*"],
      fromSources: ["local_file_system", "url", "imagesearch"],
      transformations: {
        crop: false,
        circle: true,
        rotate: true,
      },
      maxFiles: 1,
      minFiles: 1,
      maxSize: 10485760,
      onFileUploadFinished: (data) => {
        onDataChange(`embedConfig.icon`, {
          isPreMade: false,
          svgId: 0,
          url: data?.url,
        });
      },
    };
    const picker = client.picker(options);
    picker.open();
  };

  const url = window.location.href.split("/").slice(-1)[0];
  const embedData = `<div><div class='op-interactive' id='${botId}' data-title='${encodeURIComponent(
    botName,
  )}' data-url='${
    environmentData.ENVIRONMENT === "production"
      ? environmentData.LIVEBOT_URL.replace("<subDomain>", subDomain)
      : environmentData.LIVEBOT_URL
  }/${url}' data-width='${
    inPageCustomEmbed
      ? inPageCustomEmbed?.width + inPageCustomEmbed?.widthUnit
      : "100%"
  }'  data-height='${
    inPageCustomEmbed
      ? inPageCustomEmbed?.height + inPageCustomEmbed?.heightUnit
      : "100%"
  }'></div>
  <script src='${
    environmentData.ENVIRONMENT === "production"
      ? environmentData.LIVEBOT_URL.replace("<subDomain>", subDomain)
      : environmentData.LIVEBOT_URL
  }/inPageEmbed.js'></script><script>initIframe('${botId}');</script>
  </div>`; //data-debug=true
  const popupData = `<script type="text/javascript">window.LIVE_URL="${
    environmentData.ENVIRONMENT === "production"
      ? environmentData.LIVEBOT_URL.replace("<subDomain>", subDomain)
      : environmentData.LIVEBOT_URL
  }/${url}?embed=true",d=document,s=d.createElement("script"),s.src="${
    environmentData.ENVIRONMENT === "production"
      ? environmentData.LIVEBOT_URL.replace("<subDomain>", subDomain)
      : environmentData.LIVEBOT_URL
  }/embed.js",s.defer=1,d.getElementsByTagName("head")[0].appendChild(s)</script>`;
  {
    /* const popupData = `<script type="text/javascript">window.BASE_URL="${environmentData.LIVEBOT_URL}",window.BOT_ID="${url}",d=document,s=d.createElement("script"),s.src="${environmentData.LIVEBOT_URL}/embed.js",s.defer=1,d.getElementsByTagName("head")[0].appendChild(s)</script>`; */
  }
  // bot?botName=${botName}&url=
  const copyToClipboardChatStyle = async () => {
    try {
      setShowTooltip(true);
      await navigator.clipboard.writeText(popupData);
    } catch (err) {
      console.log(err);
    } finally {
      setTimeout(() => {
        setShowTooltip(false);
      }, 2000);
    }
  };

  const copyToClipboardInPage = async () => {
    try {
      setShowTooltip(true);
      await navigator.clipboard.writeText(embedData);
    } catch (err) {
      console.log(err);
    } finally {
      setTimeout(() => {
        setShowTooltip(false);
      }, 2000);
    }
  };

  const dropdownRef = useRef<any>();

  function getIconByIndex(id) {
    return botIcons.find((item) => item.id === id)?.icon || null;
  }

  const clickOut = (event: Event) => {
    if (dropdownRef?.current && !dropdownRef.current.contains(event.target)) {
      setShow(initialShowStates);
    }
  };

  useEffect(() => {
    document.addEventListener("mousedown", clickOut);
    return () => {
      document.removeEventListener("mousedown", clickOut);
    };
  }, [clickOut]);

  useEffect(() => {
    if (embedStyle === "inpage") setShow({ ...show, displayAppearance: 1 });
  }, [embedStyle]);

  return (
    <Fragment>
      {!chatConfig ? (
        "loading..."
      ) : (
        <div className="general-container embed__v2">
          {showtooptip && (
            <div className="action-done-tooltip embed-copied-action">
              <svg
                width="14"
                height="14"
                viewBox="0 0 14 14"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M7 0C3.14035 0 0 3.14035 0 7C0 10.8596 3.14035 14 7 14C10.8596 14 14 10.8596 14 7C14 3.14035 10.8596 0 7 0ZM10.9123 5.15789L6.4386 9.59649C6.17544 9.85965 5.75439 9.87719 5.47368 9.61403L3.10526 7.45614C2.82456 7.19298 2.80702 6.75439 3.05263 6.47368C3.31579 6.19298 3.75439 6.17544 4.03509 6.4386L5.91228 8.1579L9.91228 4.15789C10.193 3.87719 10.6316 3.87719 10.9123 4.15789C11.193 4.4386 11.193 4.87719 10.9123 5.15789Z"
                  fill="white"
                />
              </svg>
              Copied to Clipboard!
            </div>
          )}
          {/* bot?botName=${botName}&url= */}
          {/* https://dlvkyia8i4zmz.cloudfront.net/SMa4K1VkSJmNX6Y6wvRb_bgMobile.png */}

          <div className="embed-flx-bx gap-20">
            <div
              className="embed__demo"
            
            >
              {embedStyle === "chatstyle" && (
                <div className="tabs__container">
                  <ul className="devices__tab">
                    {tabItems.map((item, idx) => (
                      <li
                        key={idx}
                        className={`${
                          show.displayAppearance === idx + 1 &&
                          "active__device__tab"
                        }`}
                        onClick={() => handleShow("displayAppearance", idx + 1)}
                      >
                        {item.icon}
                        <p>{item.tabName}</p>
                      </li>
                    ))}
                  </ul>
                </div>
              )}

              <div
                style={{
                  height:
                    embedStyle === "inpage"
                      ? "calc(100vh - 220px)"
                      : "calc(100vh - 330px)",
                }}
                className={`embed-main fx jstfy-end ${
                  embedStyle === "inpage" && "no__bg"
                }
                ${
                  show.displayAppearance === 3
                    ? "mobile__view"
                    : show.displayAppearance === 2
                      ? "tablet__view"
                      : "dekstop__view"
                }`}
              >
                {embedStyle === "inpage" && (
                  <div className="embed__inpage">
                    <Preview
                      isTrained={true}
                      key={""}
                      chatConfig={chatConfig}
                      embed={true}
                      environment={"dev"}
                    />
                  </div>
                )}

                {embedStyle === "chatstyle" && (
                  <div className="popup__container mt-20">
                    <div
                      className={`embed__inner ${
                        !chatConfig.omniBranding ? "branding__height" : ""
                      }`}
                      style={{
                        opacity: `${!chatPopup ? "0" : "1"}`,
                        visibility: `${!chatPopup ? "hidden" : "visible"}`,
                      }}
                    >
                      <Preview
                        key={false}
                        chatConfig={chatConfig}
                        embed={true}
                        environment={"dev"}
                      />

                      {show.displayAppearance === 3 && (
                        <div
                          className="cross__btn flex-cnt mobile__cross"
                          onClick={() =>
                            // handleShow("chatbotPopup", false)
                            setChatPopup(false)
                          }
                        >
                          <CloseIcon />
                        </div>
                      )}
                    </div>

                    {embedConfig.displayBehavior === "popUpMsg" &&
                      show.popUpMessage && (
                        <div
                          className={`embed__custom__text  ${
                            show.displayAppearance === 2 &&
                            "embed__custom__text__mobile"
                          }`}
                        >
                          <div
                            className="cross__btn flex-cnt"
                            // onClick={() => onDataChange("embedConfig.displayBehavior", "default")}
                            onClick={() => handleShow("popUpMessage", false)}
                          >
                            <svg
                              width="10"
                              height="10"
                              viewBox="0 0 10 10"
                              fill="#6D6D6D"
                              xmlns="http://www.w3.org/2000/svg"
                            >
                              <path
                                d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                                fill="#6D6D6D"
                              />
                            </svg>
                          </div>

                          <p>{embedConfig?.popUpMsg}</p>
                        </div>
                      )}

                    <div
                      className={`chat__head flex-cnt ${
                        show.displayAppearance === 3 && "chat__head__mobile"
                      }`}
                      onClick={() => {
                        // if(!show.popUpMessage)
                        setChatPopup(!chatPopup);
                        handleShow("popUpMessage", false);

                        // if (embedConfig.displayBehavior !== "popUpMsg") {
                        //   // handleShow("chatbotPopup", !show.chatbotPopup);

                        // }

                        // onDataChange("embedConfig.displayBehavior", "default");
                      }}
                      style={{
                        background: embedConfig.backgroundColor,
                        color: embedConfig.color,
                      }}
                    >
                      <div
                        style={{
                          opacity: `${
                            chatPopup && show.displayAppearance !== 3
                              ? "1"
                              : "0"
                          }`,
                          visibility: `${
                            chatPopup && show.displayAppearance !== 3
                              ? "visible"
                              : "hidden"
                          }`,
                          transform: `${
                            chatPopup && show.displayAppearance !== 3
                              ? "rotate(90deg)"
                              : "rotate(0deg)"
                          }`,
                        }}
                        className="embed__chat__icon"
                      >
                        <Chat_Head_Close />
                      </div>

                      <div
                        style={{
                          opacity: `${!chatPopup ? "1" : "0"}`,
                          transform: `${
                            chatPopup ? "rotate(45deg)" : "rotate(0deg)"
                          }`,
                        }}
                        className="embed__chat__icon"
                      >
                        {!embedConfig?.icon?.isPreMade ? (
                          <img
                            src={embedConfig?.icon?.url}
                            alt="custom-embed-icon"
                          />
                        ) : (
                          getIconByIndex(embedConfig.icon.svgId)
                        )}
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </div>
            {/* <div className="vl"></div> */}
            <div
              className={`embed-code-box embed__code__box__v2 config-bot-embed ${
                embedStyle === "chatstyle" ? "width_42_percent" : ""
              }`}
            >
              <ul className="select__embed__style ">
                <li
                  className={`flex-cnt  ${
                    embedStyle === "inpage" && "active__embed__style"
                  } `}
                  onClick={() => setEmbedStyle("inpage")}
                >
                  <p>Inpage Embed Details</p>
                </li>
                <li
                  className={`flex-cnt ${
                    embedStyle === "chatstyle" && "active__embed__style"
                  }`}
                  onClick={() => setEmbedStyle("chatstyle")}
                >
                  <p>Chat Embed Details</p>
                </li>
              </ul>
              {embedStyle === "inpage" && (
                <div>
                  <div className="code-box no-mt">
                    {/* <h3 className="font-16">Inpage Embed Details</h3>
                    <p>Set trigger event for chatbot to trigger the bot to visitor</p> */}
                    <div className="embed_outer">
                      <div className="embed-head">
                        <span>In Page Embed</span>
                        <button
                          className="copy-icon copy-btn"
                          onClick={copyToClipboardInPage}
                        >
                          <svg
                            width="20"
                            height="20"
                            viewBox="0 0 20 20"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                          >
                            <g clipPath="url(#clip0_6925_12323)">
                              <path
                                d="M10.8334 16.6667C11.9381 16.6654 12.9971 16.2259 13.7782 15.4448C14.5593 14.6637 14.9988 13.6047 15.0001 12.5V5.20251C15.0014 4.76448 14.9157 4.33055 14.7481 3.92586C14.5804 3.52118 14.3341 3.1538 14.0234 2.84501L12.1551 0.976681C11.8463 0.665993 11.4789 0.419683 11.0742 0.252031C10.6695 0.0843791 10.2356 -0.00128163 9.79758 1.44913e-05H5.83341C4.72875 0.00133771 3.66971 0.440749 2.8886 1.22186C2.10748 2.00298 1.66807 3.06202 1.66675 4.16668V12.5C1.66807 13.6047 2.10748 14.6637 2.8886 15.4448C3.66971 16.2259 4.72875 16.6654 5.83341 16.6667H10.8334ZM3.33341 12.5V4.16668C3.33341 3.50364 3.59681 2.86776 4.06565 2.39891C4.53449 1.93007 5.17037 1.66668 5.83341 1.66668C5.83341 1.66668 9.93258 1.67835 10.0001 1.68668V3.33335C10.0001 3.77538 10.1757 4.1993 10.4882 4.51186C10.8008 4.82442 11.2247 5.00001 11.6667 5.00001H13.3134C13.3217 5.06751 13.3334 12.5 13.3334 12.5C13.3334 13.1631 13.07 13.7989 12.6012 14.2678C12.1323 14.7366 11.4965 15 10.8334 15H5.83341C5.17037 15 4.53449 14.7366 4.06565 14.2678C3.59681 13.7989 3.33341 13.1631 3.33341 12.5ZM18.3334 6.66668V15.8333C18.3321 16.938 17.8927 17.997 17.1116 18.7782C16.3305 19.5593 15.2714 19.9987 14.1667 20H6.66675C6.44573 20 6.23377 19.9122 6.07749 19.7559C5.92121 19.5997 5.83341 19.3877 5.83341 19.1667C5.83341 18.9457 5.92121 18.7337 6.07749 18.5774C6.23377 18.4211 6.44573 18.3333 6.66675 18.3333H14.1667C14.8298 18.3333 15.4657 18.07 15.9345 17.6011C16.4034 17.1323 16.6667 16.4964 16.6667 15.8333V6.66668C16.6667 6.44567 16.7545 6.23371 16.9108 6.07743C17.0671 5.92115 17.2791 5.83335 17.5001 5.83335C17.7211 5.83335 17.9331 5.92115 18.0893 6.07743C18.2456 6.23371 18.3334 6.44567 18.3334 6.66668Z"
                                fill="#444444"
                              />
                            </g>
                            <defs>
                              <clipPath id="clip0_6925_12323">
                                <rect width="20" height="20" fill="white" />
                              </clipPath>
                            </defs>
                          </svg>
                        </button>
                      </div>
                      <div className="embed-content">
                        <p>{embedData}</p>
                      </div>
                    </div>
                  </div>
                </div>
              )}

              {/* <div className="hr__line" /> */}

              {embedStyle === "chatstyle" && (
                <div className="code-box no-mt">
                  <div className="embed_outer mb-20">
                    <div className="embed-head">
                      <span>Chat Style Pop Up Embed</span>
                      <button
                        className="copy-icon copy-btn"
                        onClick={copyToClipboardChatStyle}
                      >
                        <svg
                          width="20"
                          height="20"
                          viewBox="0 0 20 20"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <g clipPath="url(#clip0_6925_12323)">
                            <path
                              d="M10.8334 16.6667C11.9381 16.6654 12.9971 16.2259 13.7782 15.4448C14.5593 14.6637 14.9988 13.6047 15.0001 12.5V5.20251C15.0014 4.76448 14.9157 4.33055 14.7481 3.92586C14.5804 3.52118 14.3341 3.1538 14.0234 2.84501L12.1551 0.976681C11.8463 0.665993 11.4789 0.419683 11.0742 0.252031C10.6695 0.0843791 10.2356 -0.00128163 9.79758 1.44913e-05H5.83341C4.72875 0.00133771 3.66971 0.440749 2.8886 1.22186C2.10748 2.00298 1.66807 3.06202 1.66675 4.16668V12.5C1.66807 13.6047 2.10748 14.6637 2.8886 15.4448C3.66971 16.2259 4.72875 16.6654 5.83341 16.6667H10.8334ZM3.33341 12.5V4.16668C3.33341 3.50364 3.59681 2.86776 4.06565 2.39891C4.53449 1.93007 5.17037 1.66668 5.83341 1.66668C5.83341 1.66668 9.93258 1.67835 10.0001 1.68668V3.33335C10.0001 3.77538 10.1757 4.1993 10.4882 4.51186C10.8008 4.82442 11.2247 5.00001 11.6667 5.00001H13.3134C13.3217 5.06751 13.3334 12.5 13.3334 12.5C13.3334 13.1631 13.07 13.7989 12.6012 14.2678C12.1323 14.7366 11.4965 15 10.8334 15H5.83341C5.17037 15 4.53449 14.7366 4.06565 14.2678C3.59681 13.7989 3.33341 13.1631 3.33341 12.5ZM18.3334 6.66668V15.8333C18.3321 16.938 17.8927 17.997 17.1116 18.7782C16.3305 19.5593 15.2714 19.9987 14.1667 20H6.66675C6.44573 20 6.23377 19.9122 6.07749 19.7559C5.92121 19.5997 5.83341 19.3877 5.83341 19.1667C5.83341 18.9457 5.92121 18.7337 6.07749 18.5774C6.23377 18.4211 6.44573 18.3333 6.66675 18.3333H14.1667C14.8298 18.3333 15.4657 18.07 15.9345 17.6011C16.4034 17.1323 16.6667 16.4964 16.6667 15.8333V6.66668C16.6667 6.44567 16.7545 6.23371 16.9108 6.07743C17.0671 5.92115 17.2791 5.83335 17.5001 5.83335C17.7211 5.83335 17.9331 5.92115 18.0893 6.07743C18.2456 6.23371 18.3334 6.44567 18.3334 6.66668Z"
                              fill="#444444"
                            />
                          </g>
                          <defs>
                            <clipPath id="clip0_6925_12323">
                              <rect width="20" height="20" fill="white" />
                            </clipPath>
                          </defs>
                        </svg>
                      </button>
                    </div>
                    <div className="embed-content">
                      <p>{popupData}</p>
                    </div>
                  </div>
                  <h3 className="font-16">
                    ChatBot Launcher - Display Customization
                  </h3>
                  <p className="mb-20">
                    Set the trigger event that will launch the ChatBot for the
                    visitor.
                  </p>
                  <div className="customize__chat__box">
                    {/* <h3 className="">Customize Chat window button</h3> */}

                    <div className="embed__customise fx gap-20">
                      {/* <div className="btn-preview-embed">
                        <label>Button preview</label>
                        <div className="flex-cnt">
                          <div
                            className="chat-icon-custom flex-cnt"
                            style={{ color: `${embedConfig?.color}`, background: `${embedConfig?.backgroundColor}` }}
                          >
                            {!embedConfig?.icon?.isPreMade ? (
                              <img src={embedConfig?.icon?.url} alt="custom-embed-icon" />
                            ) : (
                              getIconByIndex(embedConfig.icon.svgId)
                            )}
                          </div>
                        </div>
                      </div> */}

                      <div className="w-100">
                        <div className="btn-cutomization">
                          <label>Select icon</label>
                          <div className="select__icons">
                            {botIcons.map((data: any, i: number) => (
                              <div
                                key={i}
                                className={`chat__icon ${
                                  data.id === embedConfig?.icon.svgId
                                    ? "chat__icon__active"
                                    : ""
                                }`}
                                // onClick={() => handleMetaData("iconIndex", i)}
                                onClick={() => {
                                  onDataChange("embedConfig.icon", {
                                    isPreMade: true,
                                    svgId: data.id,
                                    url: embedConfig?.icon?.url,
                                  });
                                }}
                              >
                                {data.icon}
                              </div>
                            ))}
                            <div
                              className="upload__icon"
                              onClick={() => {
                                if (embedConfig?.icon?.url) {
                                  onDataChange("embedConfig.icon", {
                                    isPreMade: false,
                                    svgId: 0,
                                    url: embedConfig?.icon?.url,
                                  });
                                } else uploadCustomIcon();
                              }}
                            >
                              <div
                                className={`select__uploaded flex-cnt ${
                                  !embedConfig?.icon?.isPreMade
                                    ? "active__uploaded"
                                    : ""
                                }`}
                              >
                                <img
                                  src={
                                    embedConfig?.icon?.url.trim()?.length > 0
                                      ? embedConfig?.icon?.url
                                      : "https://dlvkyia8i4zmz.cloudfront.net/LCPtGMwTQaaHG5Bf2RSD_Upload_icon.svg"
                                  }
                                  alt=""
                                />
                                <div
                                  className="remove-uploaded-avatar"
                                  onClick={(e) => {
                                    e.stopPropagation();
                                    onDataChange("embedConfig.icon", {
                                      isPreMade: true,
                                      svgId: 1,
                                      url: "",
                                    });
                                  }}
                                >
                                  <img
                                    src="https://dlvkyia8i4zmz.cloudfront.net/1aMmB1VQKaY6fPnSGOUI_cross.svg"
                                    alt=""
                                  />
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>

                        <div className="btn-cutomization mt-15 btn-customization-large">
                          <label>Select colour</label>
                          <div className="select__colors">
                            <div>
                              <p>Background</p>
                              <div className="color__inputs">
                                <div
                                  className="color__box"
                                  style={{
                                    background: `${embedConfig.backgroundColor}`,
                                  }}
                                  onClick={() => {
                                    handleShow(
                                      "iconColorPicker",
                                      false,
                                      "backgroundColorPicker",
                                      !show.backgroundColorPicker,
                                    );
                                  }}
                                  onMouseDown={(e) => e.stopPropagation()}
                                ></div>

                                {show.backgroundColorPicker && (
                                  <div
                                    className="color__picker"
                                    ref={dropdownRef}
                                  >
                                    <ChromePicker
                                      color={embedConfig.backgroundColor}
                                      onChange={(color) =>
                                        onDataChange(
                                          "embedConfig.backgroundColor",
                                          color.hex,
                                        )
                                      }
                                    />
                                  </div>
                                )}

                                <input
                                  type="text"
                                  value={embedConfig.backgroundColor}
                                  onChange={(e) => {
                                    onDataChange(
                                      "embedConfig.backgroundColor",
                                      e.target.value.trim(),
                                    );
                                    // handleValidation("hex", e.target.value);
                                  }}
                                />
                              </div>
                            </div>
                            <div>
                              <p>Icon</p>
                              <div className="color__inputs">
                                <div
                                  className="color__box"
                                  style={{ background: `${embedConfig.color}` }}
                                  onClick={() => {
                                    handleShow(
                                      "backgroundColorPicker",
                                      false,
                                      "iconColorPicker",
                                      !show.iconColorPicker,
                                    );
                                  }}
                                  onMouseDown={(e) => e.stopPropagation()}
                                ></div>
                                {show.iconColorPicker && (
                                  <div
                                    className="color__picker"
                                    ref={dropdownRef}
                                  >
                                    <ChromePicker
                                      color={embedConfig.color}
                                      onChange={(color) =>
                                        onDataChange(
                                          "embedConfig.color",
                                          color.hex,
                                        )
                                      }
                                    />
                                  </div>
                                )}

                                <input
                                  type="text"
                                  value={embedConfig.color}
                                  onChange={(e) =>
                                    onDataChange(
                                      "embedConfig.color",
                                      e.target.value.trim(),
                                    )
                                  }
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="btn-cutomization responsive-btn-cutomization mt-16">
                      <label>Select colour</label>
                      <div className="select__colors">
                        <div>
                          <p>Background</p>
                          <div className="color__inputs">
                            <div
                              className="color__box"
                              style={{
                                background: `${embedConfig.backgroundColor}`,
                              }}
                              onClick={() => {
                                handleShow(
                                  "iconColorPicker",
                                  false,
                                  "backgroundColorPicker",
                                  !show.backgroundColorPicker,
                                );
                              }}
                            ></div>
                            {show.backgroundColorPicker && (
                              <div
                                onMouseDown={(e) => e.stopPropagation()}
                                className="color__picker"
                              >
                                <ChromePicker
                                  color={embedConfig.backgroundColor}
                                  onChange={(color) =>
                                    onDataChange(
                                      "embedConfig.backgroundColor",
                                      color.hex,
                                    )
                                  }
                                />
                              </div>
                            )}

                            <input
                              type="text"
                              value={embedConfig.backgroundColor}
                              onChange={(e) => {
                                onDataChange(
                                  "embedConfig.backgroundColor",
                                  e.target.value.trim(),
                                );
                                // handleValidation("hex", e.target.value);
                              }}
                            />
                          </div>
                        </div>
                        <div>
                          <p>Icon</p>
                          <div className="color__inputs">
                            <div
                              className="color__box"
                              style={{ background: `${embedConfig.color}` }}
                              onClick={() => {
                                handleShow(
                                  "backgroundColorPicker",
                                  false,
                                  "iconColorPicker",
                                  !show.iconColorPicker,
                                );
                              }}
                            ></div>
                            {show.iconColorPicker && (
                              <div
                                onMouseDown={(e) => e.stopPropagation()}
                                className="color__picker"
                              >
                                <ChromePicker
                                  color={embedConfig.color}
                                  onChange={(color) =>
                                    onDataChange("embedConfig.color", color.hex)
                                  }
                                />
                              </div>
                            )}

                            <input
                              type="text"
                              value={embedConfig.color}
                              onChange={(e) =>
                                onDataChange(
                                  "embedConfig.color",
                                  e.target.value.trim(),
                                )
                              }
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                    <form className="accordion__items mt-20">
                      <div className="acc__item">
                        <label
                          className="acc__radio jstfy-sp-btw"
                          htmlFor={"customPopup"}
                        >
                          <div className="acc__label">
                            Display custom text in a pop-up window
                          </div>
                          <div className="toggle-btn">
                            <label className="switch">
                              <input
                                type="checkbox"
                                id={"customPopup"}
                                name="custom"
                                value="popUpMsg"
                                onChange={(e) => {
                                  if (
                                    embedConfig.displayBehavior === "popUpMsg"
                                  ) {
                                    onDataChange(
                                      "embedConfig.displayBehavior",
                                      "default",
                                    );
                                    handleShow("popUpMessage", true);
                                  } else {
                                    onDataChange(
                                      "embedConfig.displayBehavior",
                                      e.target.value,
                                    );
                                    setChatPopup(false);
                                  }
                                }}
                                checked={
                                  embedConfig.displayBehavior === "popUpMsg"
                                }
                              />
                              <span className="toggle round"></span>
                            </label>
                          </div>
                        </label>
                        {embedConfig?.displayBehavior === "popUpMsg" && (
                          <div className="acc__inner__item">
                            <div className="acc__inputs fx fx-d-col">
                              <div className="fx al-cnt jstfy-sp-btw width-380">
                                <label>Text message</label>
                                <p
                                  className={
                                    embedConfig?.popUpMsg.length === 50
                                      ? "color-red"
                                      : ""
                                  }
                                >
                                  {embedConfig?.popUpMsg.length}/50
                                </p>
                              </div>

                              <input
                                className="width-380"
                                autoFocus
                                name="popup"
                                value={embedConfig?.popUpMsg}
                                maxLength={50}
                                onChange={(e) =>
                                  onDataChange(
                                    "embedConfig.popUpMsg",
                                    e.target.value,
                                  )
                                }
                                // onChange={(e)=>handleValidation(e.target.value)}
                                type="text"
                                placeholder="Got any questions? I'm happy to help."
                              />
                              {/* <span className="alert-message">Fields can't be empty*</span> */}
                            </div>
                          </div>
                        )}
                      </div>

                      {/* <div className="acc__item">
                        <label className="acc__radio" htmlFor={"chatLauncher"}>
                          <input
                            type="radio"
                            id="chatLauncher"
                            value="default"
                            onChange={(e) => {
                              onDataChange("embedConfig.displayBehavior", e.target.value);
                              handleShow("popUpMessage", true);
                            }}
                            checked={embedConfig.displayBehavior === "default"}
                          />
                          <div className="acc__label">Only show the chatbot launcher</div>
                        </label>
                      </div> */}
                    </form>
                  </div>

                  {/* Trigger event accordion */}
                  <div className="trigger__event__container">
                    {/* <h3>Set Trigger Event When</h3>
                  <p>Set trigger event for chatbot to trigger the bot to visitor</p> */}
                    <div className="trigger__accordion">
                      {/* Chat Display Behavior */}

                      <div className="display__behavior ">
                        {/* <div className="fx jstfy-sp-btw mb-20">
                          <div className="fx fx-d-col gap-2">
                            <h3>Chat Display Behavior</h3>
                            <p>Customize how the chat flow will appear to targeted visitors for desktop and mobile.</p>
                          </div>

                          <div className="toggle-btn">
                            <label className="switch">
                              <input
                                type="checkbox"
                                checked={toggle.chatDisplay}
                                onChange={() => handleToggle("chatDisplay", !toggle.chatDisplay)}
                              />
                              <span className="toggle round"></span>
                            </label>
                          </div>
                        </div> */}

                        <div className="custom__toggle">
                          <div>
                            <h3 className="font-16">
                              ChatBot Launcher Trigger Behaviour
                            </h3>
                            <p className="w-70 mb-20 mt-5">
                              Decide when the chatbot should appear to visitors.
                              The selected trigger will apply on desktop,
                              tablet, and mobile.
                            </p>
                          </div>
                          <div className="toggle-btn">
                            <label className="switch">
                              <input
                                type="checkbox"
                                name="custom"
                                onChange={(e) => {
                                  onDataChange(
                                    "embedConfig.triggerType",
                                    e.target.checked ? "duration" : "default",
                                  );
                                }}
                                checked={embedConfig.triggerType != "default"}
                              />
                              <span className="toggle round"></span>
                            </label>
                          </div>
                        </div>

                        {embedConfig.triggerType != "default" && (
                          <form className="accordion__items">
                            <div className="accordion__items">
                              <div className="acc__item">
                                <label className={`acc__radio ${embedConfig?.triggerType !== "URL" ? "acc__radio_off" : ""}`} htmlFor="trigger">
                                  <input
                                    type="radio"
                                    id="trigger"
                                    name="trigger"
                                    value="URL"
                                    onChange={(e) =>
                                      onDataChange(
                                        "embedConfig.triggerType",
                                        e.target.value,
                                      )
                                    }
                                    checked={embedConfig?.triggerType === "URL"}
                                  />
                                  <div className="acc__label">
                                    Trigger bot based on URL
                                  </div>
                                </label>

                                {embedConfig?.triggerType === "URL" && (
                                  <div className="acc__inner__item">
                                    <div className="trigger__url">
                                      <div>
                                        <div>
                                          <label>Page URL</label>
                                          <input
                                            className="mt-10"
                                            type="text"
                                            name="url"
                                            value={embedConfig.triggerURL}
                                            onChange={(e) =>
                                              onDataChange(
                                                "embedConfig.triggerURL",
                                                e.target.value.trim(),
                                              )
                                            }
                                            placeholder="http://www.example.com"
                                          />
                                        </div>
                                        {/* <div className="url__trigger__type"> */}
                                        <form
                                          action=""
                                          className="url__trigger__type"
                                        >
                                          <label
                                            className="acc__radio trigger__radio"
                                            htmlFor="type-1"
                                          >
                                            <input
                                              type="radio"
                                              id="type-1"
                                              name="typeOf"
                                              value="start"
                                              onChange={(e) =>
                                                onDataChange(
                                                  "embedConfig.triggerUrlType",
                                                  e.target.value,
                                                )
                                              }
                                              checked={
                                                embedConfig?.triggerUrlType ===
                                                "start"
                                              }
                                            />
                                            <div className="trigger__label">
                                              Begin with
                                            </div>
                                          </label>
                                          <label
                                            className="acc__radio trigger__radio"
                                            htmlFor="type-2"
                                          >
                                            <input
                                              type="radio"
                                              id="type-2"
                                              name="typeOf"
                                              value="default"
                                              onChange={(e) =>
                                                onDataChange(
                                                  "embedConfig.triggerUrlType",
                                                  e.target.value,
                                                )
                                              }
                                              checked={
                                                embedConfig?.triggerUrlType ===
                                                "default"
                                              }
                                            />
                                            <div className="trigger__label">
                                              Exact Match
                                            </div>
                                          </label>
                                        </form>
                                      </div>

                                      {/* <div>
                                      <div>
                                        <label>Duration</label>
                                        <input
                                          type="text"
                                          name="duration"
                                          value={embedConfig.triggerDuration}
                                          onChange={(e) => onDataChange("embedConfig.triggerDuration", e.target.value.trim())}
                                        />
                                      </div>
                                    </div> */}
                                    </div>
                                  </div>
                                )}
                              </div>

                              <div className="acc__item">
                                <label
                                  className={`acc__radio ${embedConfig?.triggerType !== "duration" ? "acc__radio_off" : ""}`}
                                  htmlFor="duration"
                                >
                                  <input
                                    type="radio"
                                    id="duration"
                                    name="duration"
                                    value="duration"
                                    onChange={(e) =>
                                      onDataChange(
                                        "embedConfig.triggerType",
                                        e.target.value,
                                      )
                                    }
                                    checked={
                                      embedConfig?.triggerType === "duration"
                                    }
                                  />
                                  <div className="acc__label">
                                    Trigger bot based on browsing duration
                                  </div>
                                </label>

                                {embedConfig?.triggerType === "duration" && (
                                  <div className="acc__inner__item ">
                                    {/* <div className="acc__inputs fx fx-d-col">
                                    <label>Duration</label>
                                    <input
                                      name="duration"
                                      value={embedConfig.triggerDuration}
                                      onChange={(e) => onDataChange("embedConfig.triggerDuration", e.target.value.trim())}
                                      type="text"
                                      placeholder="5"
                                    />
                                  </div> */}
                                    <label>Duration</label>
                                    <div className="fx al-cnt dropdown__input">
                                      <input
                                        type="number"
                                        name="duration"
                                        value={embedConfig.triggerDuration}
                                        onChange={(e) =>
                                          onDataChange(
                                            "embedConfig.triggerDuration",
                                            e.target.value.trim(),
                                          )
                                        }
                                        placeholder="500"
                                      />
                                      <div className="units__dropdown__outer">
                                        <div
                                          className="units__dropdown"
                                          onClick={() =>
                                            handleShow(
                                              "heightChatbot",
                                              !show.heightChatbot,
                                            )
                                          }
                                        >
                                          {embedConfig.triggerDurationUnit}
                                          <svg
                                            width="11"
                                            height="7"
                                            viewBox="0 0 11 7"
                                            fill="none"
                                            xmlns="http://www.w3.org/2000/svg"
                                          >
                                            <path
                                              d="M0 0.791992L5.20833 6.20866L10.4167 0.791992L0 0.791992Z"
                                              fill="#6D6D6D"
                                            />
                                          </svg>
                                        </div>
                                        {show.heightChatbot && (
                                          <ul
                                            className="dropdown-container units units__v2"
                                            ref={dropdownRef}
                                          >
                                            {units.map((data, i) => (
                                              <li
                                                key={i}
                                                onMouseDown={(e) =>
                                                  e.stopPropagation()
                                                }
                                                onClick={() => {
                                                  onDataChange(
                                                    "embedConfig.triggerDurationUnit",
                                                    data,
                                                  );
                                                  handleShow(
                                                    "heightChatbot",
                                                    false,
                                                  );
                                                }}
                                              >
                                                {data}
                                              </li>
                                            ))}
                                          </ul>
                                        )}
                                      </div>
                                    </div>
                                  </div>
                                )}
                              </div>
                              <div className="acc__item">
                                <label className={`acc__radio ${embedConfig?.triggerType !== "exit" ? "acc__radio_off" : ""}`}  htmlFor="exit">
                                  <input
                                    type="radio"
                                    id="exit"
                                    name="exit"
                                    value="exit"
                                    onChange={(e) =>
                                      onDataChange(
                                        "embedConfig.triggerType",
                                        e.target.value,
                                      )
                                    }
                                    checked={
                                      embedConfig?.triggerType === "exit"
                                    }
                                  />
                                  <div className="acc__label">
                                    Trigger bot for Exit Intent
                                  </div>
                                </label>
                              </div>
                            </div>
                          </form>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </Fragment>
  );
}

function CloseIcon() {
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width={10}
      height={10}
      viewBox="0 0 10 10"
      fill="none"
    >
      <g clipPath="url(#clip0_2115_5740)">
        <path
          d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
          fill="#6D6D6D"
        />
      </g>
      <defs>
        <clipPath id="clip0_2115_5740">
          <rect width={10} height={10} fill="white" />
        </clipPath>
      </defs>
    </svg>
  );
}
