import { useState, useRef } from "react";
import { produce } from "immer";
import DeleteIcon from "@/assets/DeleteIcon";
import VariableDropdown from "@/pages/omni-builder/components/VariableDropdown";
import AddIcon from "@/assets/AddIcon";
import "@/styles/downgrade.css";
import {
  COMPARISON_OPERATOR,
  LOGICAL_OPERATOR,
} from "@/pages/omni-builder/constant";
import type { Variable, Condition } from "@/pages/omni-builder/types";
import { ConditionLimits } from "@/pages/omni-builder/constant";
import placeVariable from "@/pages/omni-builder/utils/placeVariable";

export default function ExitConditionSetupModal({
  conditionData,
  nodeVars,
  responseVars,
  onDataChange,
}) {
  const valueInputRefs = useRef<Array<React.ElementRef<"input">>>([]);
  const [openDropdownId, setOpenDropdownId] = useState("");

  function addCondition() {
    if (conditionData.length >= ConditionLimits.MAX_CONDITION) return;
    const data = conditionDispatch(conditionData, {
      type: "add",
    });
    onDataChange(data);
  }

  function deleteCondition(conditionIndex: number) {
    if (conditionData.length === 1) return;

    const data = conditionDispatch(conditionData, {
      type: "remove",
      payload: {
        conditionIndex,
      },
    });
    onDataChange(data);
  }

  function handleValueChange<T extends keyof Condition>(
    conditionIndex: number,
    inputType: T,
    value: Condition[T]
  ) {
    const data = conditionDispatch(conditionData, {
      type: "valueChange",
      payload: {
        conditionIndex,
        inputType,
        value,
      },
    });
    onDataChange(data);
  }

  function handleVariableSelect(conditionIndex: number, variable: Variable) {
    const value = placeVariable(
      valueInputRefs.current[conditionIndex],
      variable
    );

    const data = conditionDispatch(conditionData, {
      type: "valueChange",
      payload: {
        conditionIndex,
        inputType: "value",
        value,
      },
    });
    onDataChange(data);
  }

  function handleDropdown(id: string) {
    setOpenDropdownId(openDropdownId ? "" : id);
  }

  return (
    <div className="data-box-container call-api-content">
      {conditionData.map((condition: Condition, conditionIndex) => (
        <div key={conditionIndex} className="inputs-data-box">
          {!!conditionIndex && (
            <div className="data-box-buttom condition">
              <span className="logical-operator-hr"></span>
              <div className="logical-operator-wrap condition">
                {LOGICAL_OPERATOR.map((operator, index) => (
                  <div
                    key={index}
                    className={`logical-operator condition ${
                      condition.logical_operator === operator
                        ? "logical-operator-sel"
                        : ""
                    }`}
                    onClick={() =>
                      handleValueChange(
                        conditionIndex,
                        "logical_operator",
                        operator
                      )
                    }
                  >
                    {operator}
                  </div>
                ))}
              </div>
              <span className="logical-operator-hr"></span>
            </div>
          )}
          <div className="data-box-label condition">
            <span className="webhook_head">Set the condition(s)</span>
            {conditionData.length > 1 && (
              <div
                className="condition-delete"
                onClick={() => deleteCondition(conditionIndex)}
              >
                <DeleteIcon />
              </div>
            )}
          </div>
          <div className="data-box-wrap grid gap-20">
            <div className="data-box-segment condition ">
              <div className="row-container fx-d-col al-fx-start gap-8">
                <span className="input-label">If @</span>
                <div
                  className="node-dropdown h-mc w-100"
                  onClick={() => handleDropdown(conditionIndex + "variable")}
                  onMouseLeave={() => handleDropdown("")}
                >
                  <input
                    className="rightSidebar-tarea condition no-mt"
                    value={condition.variable}
                    placeholder="Type or select the variable"
                    onChange={(event) =>
                      handleValueChange(
                        conditionIndex,
                        "variable",
                        event.target.value
                      )
                    }
                    readOnly
                  />
                  {openDropdownId === conditionIndex + "variable" && (
                    <div className="condition-variable-dd full-view-menu w-100">
                      <VariableDropdown
                        onSelect={(variable) =>
                          handleValueChange(
                            conditionIndex,
                            "variable",
                            variable.name
                          )
                        }
                        customVars={responseVars}
                      />
                    </div>
                  )}
                </div>
              </div>
            </div>
            <div className="data-box-segment condition">
              <span className="input-label">Condition Operators</span>
              <div className="dropdownContainer condition-operator-dropdown">
                <div
                  className="selectedContent condition-operator-dropdown-inner"
                  onClick={() => handleDropdown(conditionIndex + "operator")}
                  onMouseLeave={() => handleDropdown("")}
                >
                  <span>{condition.comparison_operator}</span>
                  <DownArrow />
                  {openDropdownId === conditionIndex + "operator" && (
                    <div className="condition-operator-dropdown-items">
                      <ul className="dropdownList condition-operator-list grey-scrollbar">
                        {COMPARISON_OPERATOR.map((operator, index) => (
                          <li
                            key={index}
                            className={
                              condition.comparison_operator === operator
                                ? "active-item"
                                : ""
                            }
                            onClick={() =>
                              handleValueChange(
                                conditionIndex,
                                "comparison_operator",
                                operator
                              )
                            }
                          >
                            {operator}
                          </li>
                        ))}
                      </ul>
                    </div>
                  )}
                </div>
              </div>
            </div>
            {!["IS DEFINED", "IS NOT DEFINED"].includes(
              condition.comparison_operator
            ) && (
              <div className="data-box-segment condition">
                <div className="row-container mt-3">
                  <span className="input-label">Enter Value</span>
                  <div
                    className="node-dropdown h-mc"
                    onClick={() => handleDropdown(conditionIndex + "value")}
                    onMouseLeave={() => handleDropdown("")}
                  >
                    <span className="color-red">Add Variable</span>
                    {openDropdownId === conditionIndex + "value" && (
                      <VariableDropdown
                        onSelect={(variable) =>
                          handleVariableSelect(conditionIndex, variable)
                        }
                        customVars={nodeVars}
                      />
                    )}
                  </div>
                </div>
                <input
                  className="rightSidebar-tarea condition no-m"
                  ref={(ref) =>
                    ref && (valueInputRefs.current[conditionIndex] = ref)
                  }
                  onChange={(event) =>
                    handleValueChange(
                      conditionIndex,
                      "value",
                      event.target.value
                    )
                  }
                  value={condition.value}
                  placeholder="Type a value or select the variable"
                />
              </div>
            )}
          </div>
        </div>
      ))}
      <p
        className="add-condition add_new_button mt-20 cursor-pointer"
        onClick={addCondition}
      >
        <AddIcon />
        Add New Condition
      </p>
    </div>
  );
}

type Action<U extends keyof Condition> =
  | { type: "add" }
  | {
      type: "remove";
      payload: {
        conditionIndex: number;
      };
    }
  | {
      type: "valueChange";
      payload: {
        conditionIndex: number;
        inputType: U;
        value: Condition[U];
      };
    };

function conditionDispatch<
  T extends Array<Condition>,
  U extends keyof Condition
>(data: T, action: Action<U>): T {
  const conditionData = produce(data, (draft) => {
    switch (action.type) {
      case "add": {
        draft.push({
          variable: "",
          value: "",
          comparison_operator: "EQUAL TO",
          logical_operator: "AND",
        });
        break;
      }
      case "remove": {
        draft.splice(action.payload.conditionIndex, 1);
        break;
      }
      case "valueChange": {
        draft[action.payload.conditionIndex][action.payload.inputType] =
          action.payload.value;
        break;
      }
      default: {
        throw new Error(`Invalid action: ${JSON.stringify(action)}`);
      }
    }
  });

  return conditionData;
}

function DownArrow() {
  return (
    <svg
      width="10"
      height="6"
      viewBox="0 0 10 6"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M1 1L5 5L9 1"
        stroke="#444444"
        strokeWidth="2"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    </svg>
  );
}
