import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { State } from "@/redux/Types";
import { userSlice } from "@/redux/User";
import { INTEGRATIONS, SOCIALS, type ConfigId } from "@/constants/ConfigData";
import LockIcon from "@/assets/LockIcon";
import environment from "@/constants/Environment";
import {
  AiChatBotShortLogo,
  DoubleArrowSvg,
  IntOmniLogoSvg,
} from "@/assets/AiBotAssets";
import useStore from "@/pages/omni-builder/store";
import { motion } from "framer-motion";

const environmentData = environment();

interface Props {
  onConfigModalOpen: (configId: ConfigId | "") => void;
}

export default function Integration({ onConfigModalOpen }: Props) {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const plan: any = useSelector((state: State) => state.user.plan);
  const [isFeatureLock, setIsFeatureLock] = useState(false);
  const isWhaptsappLoading = useStore((state) => state.isWhatsappLoading);

  useEffect(() => {
    const isFeatureLock =
      environmentData.isAI && !plan?.ai_currentLimits?.aiKey;
    setIsFeatureLock(isFeatureLock);
  }, [plan]);

  const isConfigLoading = (configName: string) => {
    if (configName === "Whatsapp") {
      return isWhaptsappLoading;
    }

    // to re-use loading, add loading conditions according to configNames
    return false;
  };
  const trial = useSelector(
    (state: State) => state.user.plan?.["trial"] ?? null,
  );

    const iswarmyEnabled = useSelector(
    (state: State) => state.user.plan?.["currentLimits"]?.["enableWarmy"],
  );

  return (
    <motion.div
      initial={{ opacity: 0, translateY: 15 }}
      animate={{ opacity: 1, translateY: 0 }}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.5 }}
      className="general-container settings-container"
    >
      {/* <div className="gen-header">
        <h1>Integrations</h1>
        <hr />
      </div> */}
      {
        <div className="settings_wrapper">
          <div className="native-container">
            <div className="native-head jstfy-sp-btw">
              <h2 className="settings_heading">Native</h2>
            </div>
            <div className="integrations mt-15">
              {INTEGRATIONS.map(
                (config, index) =>
                  (!environmentData.isAI || config.id === "openai") && (
                    <motion.div
                      initial={{ opacity: 0, translateX: 70 }}
                      animate={{ opacity: 1, translateX: 0 }}
                      transition={{ duration: 0.05 }}
                      key={index}
                      // className={`integration-box ${
                      //  (config.name === "OPEN AI" && isFeatureLock) 
                      //  ? "integration-locked"
                      //     : " "
                      // }`}
                      className={`integration-box ${(
                        (config.name === "OPEN AI" && isFeatureLock) ||
                        (config.name === "warmy" && (trial || trial === null))
                    ) ? "integration-locked" : ""}`}
                      onClick={() => {
                        if ( (config.name === "OPEN AI" && isFeatureLock) ||
                        (config.name === "warmy" && (trial || trial === null))) return;
                        onConfigModalOpen(config.id);
                      }}
                    >
                      <div className="integration-inner">
                        <div
                          className="integration-box-head"
                          style={{ backgroundColor: config.background_color }}
                        >
                          {environmentData.isAI ? (
                            <AiChatBotShortLogo />
                          ) : (
                            <IntOmniLogoSvg />
                          )}
                          <DoubleArrowSvg />
                          <img src={config.logo} />
                        </div>
                        <div className="integration-box-content">
                          <h4 className="fx al-cnt mb-10 gap-10">
                            {environmentData.isAI
                              ? "chatbotbuilder"
                              : "Omniengage"}
                            <span> + </span> {config.name}
                          </h4>
                          <p className="mb-20">{config.description}</p>
                          <span className="view_int">View Integration</span>
                        </div>
                      </div>
                      { ((config.name === "OPEN AI" && isFeatureLock) ||
                        (config.name === "warmy" && (trial || trial === null))) && (
                        <div className="locked-default-card flex-cnt fx-d-col">
                          <LockIcon />
                          <h4>Feature locked</h4>
                          <p>
                            Please upgrade your current plan to use this feature
                          </p>
                          <button
                            className="buy-feature flex-cnt"
                            onClick={() => {
                              navigate("/settings");
                              dispatch(userSlice.actions.setSettings(2));
                            }}
                          >
                            Buy Plan
                          </button>
                        </div>
                      )}
                   
                    </motion.div>
                  ),
              )}
            </div>
            {!environmentData.isAI && (
              <div>
                <div className="mt-30 native-head jstfy-sp-btw">
                  <h2 className="settings_heading">Social</h2>
                </div>
                <div className="integrations mt-15">
                  {SOCIALS.map((config, index) => (
                    <motion.div
                      initial={{ opacity: 0, translateX: 70 }}
                      animate={{ opacity: 1, translateX: 0 }}
                      transition={{ duration: 0.05 }}
                      key={index}
                      className={`integration-box ${
                        isConfigLoading(config.name) ? "loader" : ""
                      }`}
                      onClick={() => onConfigModalOpen(config.id)}
                    >
                      <div className="integration-inner">
                        <div
                          className="integration-box-head"
                          style={{ backgroundColor: config.background_color }}
                        >
                          {environmentData.isAI ? (
                            <AiChatBotShortLogo />
                          ) : (
                            <IntOmniLogoSvg />
                          )}
                          <DoubleArrowSvg />
                          <img src={config.logo} />
                        </div>
                        <div className="integration-box-content">
                          <h4 className="mb-10">
                            {environmentData.isAI
                              ? "Chatbotbuilder"
                              : "omniengage"}{" "}
                            <span>+</span> {config.name}
                          </h4>
                          <p className="mb-20">{config.description}</p>
                          <span
                            className={`view_int ${
                              isConfigLoading(config.name) ? "grey_cta" : ""
                            }`}
                          >
                            View Integration
                          </span>
                        </div>
                      </div>
                    </motion.div>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>
      }
    </motion.div>
  );
}
