import type { MutationStatus } from "@tanstack/react-query";
import SuccessIcon from "@/assets/SuccessIcon";
import ErrorIcon from "@/assets/ErrorIcon";

interface Props {
  status: MutationStatus;
}

export default function SaveStatus({ status }: Props) {
  return (
    <div className="autosave-btn">
      {status === "pending" && <p className="mr-2">Saving..</p>}
      {status === "success" && (
        <>
          <p className="inprogress">Saved</p>
          <SuccessIcon />
        </>
      )}
      {status === "error" && (
        <div className="builder-status-error">
          <p className="inprogress">Error</p>
          <ErrorIcon />
        </div>
      )}
    </div>
  );
}
