import { useState, useEffect } from "react";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import EditIcon from "@/assets/builder/EditIcon";

interface Props {
  name: string;
  onSave: (name: string) => void;
}

export default function BotName({ name, onSave }: Props) {
  const dispatch = useDispatch();
  const [inputText, setInputText] = useState(name);
  const [isEditing, setIsEditing] = useState(false);

  useEffect(() => {
    setInputText(name);
  }, [name]);

  async function updateName() {
    try {
      setIsEditing(false);
      if (inputText === name) return;
      if (inputText.length < 3 || inputText.length > 30)
        throw new Error("Bot name should be between 3 and 30 characters");
      onSave(inputText);
    } catch (error: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: error.message,
        })
      );
      setInputText(name);
    }
  }

  return (
    <>
      {isEditing ? (
        <input
          type="text"
          className="editable"
          value={inputText}
          onChange={(event) => setInputText(event.target.value)}
          onKeyDown={(event) => event.key === "Enter" && updateName()}
          onBlur={updateName}
          autoFocus
        />
      ) : (
        <div className="builder-bot-name" onClick={() => setIsEditing(true)}>
          <p className="botNameEdit">
            {inputText.length > 11 ? `${inputText.slice(0, 11)}...` : inputText}
          </p>
          <div className="flex-cnt">
            <EditIcon />
          </div>
        </div>
      )}
    </>
  );
}
