import { useState, type MouseEvent } from "react";
import { produce } from "immer";
import { Cursor, Row } from "../node-configs/HitApi";
import { JSONToString } from "@/utils/handleJson";
import { HitApiLimits } from "../constant";

export default function HitApiTable({
  data,
  onDataChange,
  onCursorChange,
  expand

}: {
  data: Array<Row>;
  onDataChange: (data: Array<Row>) => void;
  onCursorChange: (cursor: Cursor | null) => void;
  expand : boolean,
}) {
  function handleCursor(
    event: MouseEvent<HTMLInputElement>,
    rowIndex: number,
    fieldType: keyof Row
  ) {
    const result: Cursor = {
      type: "table",
      startPos:
        event.currentTarget.selectionStart ?? event.currentTarget.value.length,
      endPos:
        event.currentTarget.selectionEnd ?? event.currentTarget.value.length,
      rowIndex,
      fieldType,
    };

    onCursorChange(result);
  }
  console.log(data)

  function addRow() {
    if (data.length >= HitApiLimits.MAX_ROW) return;
    const tableData = tableDispatch(data, { type: "add" });
    onDataChange(tableData);
  }

  function deleteRow(rowIndex: number) {
    if (data.length === 1) return;
    const tableData = tableDispatch(data, {
      type: "remove",
      payload: { rowIndex },
    });
    onDataChange(tableData);
  }

  function handleValueChange<T extends keyof Row>(
    rowIndex: number,
    inputType: T,
    value: Row[T]
  ) {
    const tableData = tableDispatch(data, {
      type: "valueChange",
      payload: { rowIndex, inputType, value },
    });
    onDataChange(tableData);
  }



  return (
    <div className="header-table-container ovr-flw-x-au">
      <table className="header-table">
        <tr>
          <th className="header-key">Key</th>
          <th className="header-value">Value</th>
          <th className="header-value">Test Value</th>
          <th className="header-action">Action</th>
        </tr>
        <tbody className={expand ? `grey-scrollbar expanded-table`  : "small-table" }>
          {data.map((row, rowIndex) => (
            <tr key={rowIndex}>
              <td>
                <input
                  type="text"
                  placeholder="Enter Key"
                  value={row.key}
                  onClick={() => onCursorChange(null)}
                  onChange={(event) =>
                    handleValueChange(rowIndex, "key", event.target.value)
                  }
                />
              </td>
              <td>
                <input
                  type="text"
                  placeholder="Enter Value"
                  value={JSONToString(row.value)}
                  onClick={(event) => handleCursor(event, rowIndex, "value")}
                  onChange={(event) =>
                    handleValueChange(rowIndex, "value", event.target.value)
                  }
                />
              </td>
              <td>
                <input
                  type="text"
                  placeholder="Enter Test Value"
                  value={row.testValue}
                  onClick={() => onCursorChange(null)}
                  onChange={(event) =>
                    handleValueChange(rowIndex, "testValue", event.target.value)
                  }
                />
              </td>
              <td onClick={() => deleteRow(rowIndex)}>
                <svg
                  width="17"
                  height="16"
                  viewBox="0 0 17 16"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M12.1931 7.42857H5.62168C5.54311 7.42857 5.47882 7.49286 5.47882 7.57143V8.42857C5.47882 8.50715 5.54311 8.57143 5.62168 8.57143H12.1931C12.2717 8.57143 12.336 8.50715 12.336 8.42857V7.57143C12.336 7.49286 12.2717 7.42857 12.1931 7.42857Z"
                    fill="#FB5F66"
                  />
                  <path
                    d="M8.90741 0C4.48955 0 0.90741 3.58214 0.90741 8C0.90741 12.4179 4.48955 16 8.90741 16C13.3253 16 16.9074 12.4179 16.9074 8C16.9074 3.58214 13.3253 0 8.90741 0ZM8.90741 14.6429C5.23955 14.6429 2.26455 11.6679 2.26455 8C2.26455 4.33214 5.23955 1.35714 8.90741 1.35714C12.5753 1.35714 15.5503 4.33214 15.5503 8C15.5503 11.6679 12.5753 14.6429 8.90741 14.6429Z"
                    fill="#FB5F66"
                  />
                </svg>
              </td>
            </tr>
          ))}
        </tbody>
        <div className="add-row-btn">
          <button onClick={addRow}>
          <svg
    xmlns="http://www.w3.org/2000/svg"
    width={16}
    height={17}
    viewBox="0 0 16 17"
    fill="none"
  >
    <circle cx={8} cy={8.5} r={7.5} stroke="#FB5F66" />
    <path
      d="M7.8718 12.1352C7.78647 12.1352 7.71394 12.1054 7.6542 12.0456C7.59447 11.9859 7.5646 11.9134 7.5646 11.828V9.33205H5.107C5.02167 9.33205 4.94914 9.30218 4.8894 9.24245C4.82967 9.18272 4.7998 9.11018 4.7998 9.02485V8.34645C4.7998 8.25258 4.82967 8.18005 4.8894 8.12885C4.94914 8.06912 5.02167 8.03925 5.107 8.03925H7.5646V5.60725C7.5646 5.52192 7.59447 5.44938 7.6542 5.38965C7.71394 5.32992 7.78647 5.30005 7.8718 5.30005H8.61421C8.70807 5.30005 8.7806 5.32992 8.8318 5.38965C8.89154 5.44938 8.92141 5.52192 8.92141 5.60725V8.03925H11.379C11.4643 8.03925 11.5369 8.06912 11.5966 8.12885C11.6563 8.18005 11.6862 8.25258 11.6862 8.34645V9.02485C11.6862 9.11018 11.6563 9.18272 11.5966 9.24245C11.5369 9.30218 11.4643 9.33205 11.379 9.33205H8.92141V11.828C8.92141 11.9134 8.89154 11.9859 8.8318 12.0456C8.7806 12.1054 8.70807 12.1352 8.61421 12.1352H7.8718Z"
      fill="#FB5F66"
    />
  </svg>
            Add New Row
          </button>
        </div>
      </table>
    </div>
  );
}

type Action<U extends keyof Row> =
  | { type: "add" }
  | {
      type: "remove";
      payload: {
        rowIndex: number;
      };
    }
  | {
      type: "valueChange";
      payload: {
        rowIndex: number;
        inputType: U;
        value: Row[U];
      };
    };

function tableDispatch<T extends Array<Row>, U extends keyof Row>(
  data: T,
  action: Action<U>
): T {
  const tableData = produce(data, (draft) => {
    switch (action.type) {
      case "add": {
        draft.push({
          key: "",
          value: "",
          testValue: "",
        });
        break;
      }
      case "remove": {
        draft.splice(action.payload.rowIndex, 1);
        break;
      }
      case "valueChange": {
        draft[action.payload.rowIndex][action.payload.inputType] =
          action.payload.value.trim();
        break;
      }
      default: {
        throw new Error(`Invalid action: ${JSON.stringify(action)}`);
      }
    }
  });

  return tableData;
}
