import { type Node } from "@xyflow/react";
import { useState, useMemo } from "react";
import APICallSVG from "@/assets/ConfigureImages/APICallSVG";
import DeliverySVG from "@/assets/ConfigureImages/DeliverySVG";
import SMSFailureSVG from "@/assets/ConfigureImages/SMSFailureSVG";
import WebhookSVG from "@/assets/ConfigureImages/WebhookSVG";
import ReconfigureSVG from "@/assets/ConfigureImages/ReconfigureSVG";
import ExitCallApiModal from "./ExitCallApiModal";
import ExitWebhookModal from "./ExitWebhookModal";
import useStore from "@/pages/omni-builder/store";
import { getVariableData } from "@/pages/omni-builder/store";
import type { Variables } from "@/pages/omni-builder/types";
import type { SomeRequired } from "@/types/util";
import { easeInOut, motion, Variants } from "framer-motion";

const fadeInUpAnimation: Variants = {
  hidden: {
    opacity: 0,
    y: -50,
  },
  show: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.3,
      ease: easeInOut,
    },
  },
};

interface Props {
  onDataChange: (field: string, value: any) => void;
}

export default function ExitCondition({ onDataChange }: Props) {
  const { botCustomId, exitConfig } = useStore((state) => state.metaData);
  const nodes = useStore((state) => state.nodes);
  const nodeVars = useMemo(() => findVariables(nodes), [nodes]);
  const [openDropdownId, setOpenDropdownId] = useState<DropDownId>("");
  const [showApiModal, setShowApiModal] = useState(false);
  const [showWebhookModal, setShowWebhookModal] = useState(false);

  function handleApiValueChange(field: string, value: any) {
    if (exitConfig?.api.apiData) {
      onDataChange("exitConfig.api." + field, value);
    } else {
      onDataChange("exitConfig.api.apiData", {
        ...(exitConfig.api.apiData ?? apiInit),
        [field]: value,
      });
    }
  }

  function handleWebhookValueChange(field: string, value: any) {
    if (exitConfig?.webhook.webhookData) {
      onDataChange("exitConfig.webhook." + field, value);
    } else {
      onDataChange("exitConfig.webhook.webhookData", {
        ...(exitConfig.webhook.webhookData ?? webhookInit),
        [field]: value,
      });
    }
  }

  function handleNodeSelect(nodeId: string, type: string) {
    const nodeAlreadyAdded = exitConfig[type].nodeIds?.includes(nodeId);
    if (nodeAlreadyAdded) {
      onDataChange(
        `exitConfig.${type}.nodeIds`,
        exitConfig[type].nodeIds.filter((id: string) => id !== nodeId),
      );
    } else {
      onDataChange(`exitConfig.${type}.nodeIds`, [
        ...exitConfig[type].nodeIds,
        nodeId,
      ]);
    }
  }

  function handleDropdown(id: DropDownId) {
    setOpenDropdownId(openDropdownId ? "" : id);
  }

  return (
    <motion.div
      initial={{ opacity: 0, translateY: 15 }}
      animate={{ opacity: 1, translateY: 0 }}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.25 }}
      className="general-container settings-container"
      style={{ background: "#fafafa", height: "100%" }}
    >
      <div className="configure-main">
        <h3>Setup exit conditions</h3>
        <div className="configure-data mt-15 gap-15 pd-30 grid">
          <div className="tab-exit">
            <div className="outer-tab pd-20 fx jstfy-sp-btw">
              <div className="left-tab-exit fx gap-20">
                <div className="tab-exit-i flex-cnt">
                  <DeliverySVG />
                </div>
                <div className="tab-exit-c grid gap-8">
                  <div className="fx al-cnt gap-8">
                    <h3>Email Delivery Failure</h3> <InfoIconSVG />
                  </div>
                  <p>When there is a delivery failure on Email</p>
                </div>
              </div>
              <div className="flex-cnt gap-20">
                <div className="toggle-btn">
                  <label className="switch">
                    <input
                      type="checkbox"
                      name="custom"
                      checked={exitConfig?.email.enable}
                      onChange={() =>
                        onDataChange(
                          "exitConfig.email.enable",
                          !exitConfig?.email.enable,
                        )
                      }
                    />
                    <span className="toggle round"></span>
                  </label>
                </div>
              </div>
            </div>
            {exitConfig?.email.enable && (
              <motion.div
                initial="hidden"
                animate="show"
                variants={fadeInUpAnimation}
                className="inner-tab pd-20"
                onMouseLeave={() => handleDropdown("")}
              >
                <span className="input-label">Select email nodes</span>
                <div className="dropdown">
                  <div
                    className="node-dropdown d-block wd-100 mb-10"
                    onClick={() => handleDropdown("email")}
                  >
                    <div
                      className="rightSidebar-tarea fx jstfy-sp-btw al-cnt"
                      style={{ width: "385px" }}
                    >
                      {!exitConfig.email.nodeIds?.length
                        ? "All email nodes"
                        : `Selected Nodes - ${exitConfig.email.nodeIds.join(
                            ", ",
                          )}`}
                      <svg
                        width="10"
                        height="6"
                        viewBox="0 0 10 6"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M1 1L5 5L9 1"
                          stroke="#444444"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        ></path>
                      </svg>
                    </div>
                    {openDropdownId === "email" && (
                      <div
                        className="dropdown-items segment-dropdown exit-drp p-0"
                        style={{ width: "385px", left: "0" }}
                      >
                        <div
                          onClick={() =>
                            onDataChange("exitConfig.email.nodeIds", [])
                          }
                          className="fx al-cnt multi-list pd-10 gap-10"
                        >
                          <div className="multi-select-drp p-relative fx al-cnt gap-20">
                            <input
                              type="checkbox"
                              id="checkbox3"
                              value="O"
                              checked={!exitConfig.email.nodeIds?.length}
                            />
                            <label htmlFor="checkbox3"></label>
                          </div>
                          All email nodes
                        </div>
                        <ul className="grey-scrollbar multi-select-list">
                          {nodes.map(
                            (node) =>
                              node.type === "sendEmail" && (
                                <li
                                  onClick={
                                    () => handleNodeSelect(node.id, "email")
                                    // onDataChange(
                                    //   "exitConfig.email.nodeIds",
                                    //   node.id
                                    // )
                                  }
                                >
                                  <div className="multi-select-drp p-relative fx al-cnt gap-20">
                                    <input
                                      type="checkbox"
                                      id="checkbox3"
                                      value="O"
                                      checked={exitConfig.email.nodeIds?.includes(
                                        node.id,
                                      )}
                                    />
                                    <label htmlFor="checkbox3"></label>
                                  </div>
                                  Node - {node.id}
                                </li>
                              ),
                          )}
                        </ul>
                      </div>
                    )}
                  </div>
                </div>
                <span className="input-label">Select condition</span>
                <div className="fx fx-wrap mt-10 gap-8">
                  {EMAIL_CONDITION.map((condition) => (
                    <div
                      key={condition.id}
                      className={
                        exitConfig?.email.events[condition.id]
                          ? `checklist chck-active`
                          : `checklist`
                      }
                    >
                      <div className="check-box-container flex-cnt">
                        <div className="round-c">
                          <input
                            type="checkbox"
                            id={`email_${condition.id}`}
                            checked={exitConfig?.email.events[condition.id]}
                            onChange={() =>
                              onDataChange(
                                `exitConfig.email.events.${condition.id}`,
                                !exitConfig?.email.events[condition.id],
                              )
                            }
                          />
                          <label htmlFor={`email_${condition.id}`}></label>
                        </div>
                        <p>{condition.label}</p>
                      </div>
                    </div>
                  ))}
                </div>
              </motion.div>
            )}
          </div>
          <div className="tab-exit">
            <div className="outer-tab pd-20 fx jstfy-sp-btw">
              <div className="left-tab-exit fx gap-20">
                <div className="tab-exit-i flex-cnt">
                  <SMSFailureSVG />
                </div>
                <div className="tab-exit-c grid gap-8">
                  <div className="fx al-cnt gap-8">
                    <h3>SMS Delivery Failure</h3> <InfoIconSVG />
                  </div>
                  <p>When there is a delivery failure on SMS</p>
                </div>
              </div>
              <div className="flex-cnt gap-20">
                <div className="toggle-btn">
                  <label className="switch">
                    <input
                      type="checkbox"
                      name="custom"
                      checked={exitConfig?.sms.enable}
                      onChange={() =>
                        onDataChange(
                          "exitConfig.sms.enable",
                          !exitConfig?.sms.enable,
                        )
                      }
                    />
                    <span className="toggle round"></span>
                  </label>
                </div>
              </div>
            </div>
            {exitConfig?.sms.enable && (
              <motion.div
                initial="hidden"
                animate="show"
                variants={fadeInUpAnimation}
                className="inner-tab pd-20"
                onMouseLeave={() => handleDropdown("")}
              >
                <span className="input-label">Select sms nodes</span>
                <div className="dropdown">
                  <div
                    className="node-dropdown d-block wd-100 mb-10"
                    onClick={() => handleDropdown("sms")}
                  >
                    <div
                      className="rightSidebar-tarea fx jstfy-sp-btw al-cnt"
                      style={{ width: "385px" }}
                    >
                      {!exitConfig.sms.nodeIds?.length
                        ? "All SMS nodes"
                        : `Selected Nodes - ${exitConfig.sms.nodeIds.join(
                            ", ",
                          )}`}
                      <svg
                        width="10"
                        height="6"
                        viewBox="0 0 10 6"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M1 1L5 5L9 1"
                          stroke="#444444"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        ></path>
                      </svg>
                    </div>
                    {openDropdownId === "sms" && (
                      <div
                        className="dropdown-items segment-dropdown exit-drp p-0"
                        style={{ width: "385px", left: "0" }}
                      >
                        <div
                          onClick={() =>
                            onDataChange("exitConfig.sms.nodeIds", [])
                          }
                          className="fx al-cnt multi-list pd-10 gap-10"
                        >
                          <div className="multi-select-drp p-relative fx al-cnt gap-20">
                            <input
                              type="checkbox"
                              id="checkbox3"
                              value="O"
                              checked={!exitConfig.sms.nodeIds?.length}
                            />
                            <label htmlFor="checkbox3"></label>
                          </div>
                          All sms nodes
                        </div>
                        <ul className="grey-scrollbar multi-select-list">
                          {nodes.map(
                            (node) =>
                              node.type === "sendSMS" && (
                                <li
                                  onClick={
                                    () => handleNodeSelect(node.id, "sms")
                                    // onDataChange(
                                    //   "exitConfig.sms.nodeId",
                                    //   node.id
                                    // )
                                  }
                                >
                                  <div className="multi-select-drp p-relative fx al-cnt gap-20">
                                    <input
                                      type="checkbox"
                                      id="checkbox3"
                                      value="O"
                                      checked={exitConfig.sms.nodeIds?.includes(
                                        node.id,
                                      )}
                                    />
                                    <label htmlFor="checkbox3"></label>
                                  </div>
                                  Node - {node.id}
                                </li>
                              ),
                          )}
                        </ul>
                      </div>
                    )}
                  </div>
                </div>
                <span className="input-label">Select condition</span>
                <div className="fx fx-wrap mt-10 gap-8">
                  {SMS_CONDITION.map((condition) => (
                    <div
                      key={condition.id}
                      className={
                        exitConfig?.sms.events[condition.id]
                          ? `checklist chck-active`
                          : `checklist`
                      }
                    >
                      <div className="check-box-container flex-cnt">
                        <div className="round-c">
                          <input
                            type="checkbox"
                            id={`sms_${condition.id}`}
                            checked={exitConfig?.sms.events[condition.id]}
                            onChange={() =>
                              onDataChange(
                                `exitConfig.sms.events.${condition.id}`,
                                !exitConfig?.sms.events[condition.id],
                              )
                            }
                          />
                          <label htmlFor={`sms_${condition.id}`}></label>
                        </div>
                        <p>{condition.label}</p>
                      </div>
                    </div>
                  ))}
                </div>
              </motion.div>
            )}
          </div>
          <div className="tab-exit">
            <div className="outer-tab pd-20 fx jstfy-sp-btw">
              <div className="left-tab-exit fx gap-20">
                <div className="tab-exit-i flex-cnt">
                  <APICallSVG />
                </div>
                <div className="tab-exit-c grid gap-8">
                  <div className="fx al-cnt gap-8">
                    <h3>API call</h3> <InfoIconSVG />
                  </div>
                  <p>
                    When a particular condition is satisfied on an API Call
                    Outcome {">"} Setup API Call Condition{" "}
                  </p>
                </div>
              </div>
              <div className="flex-cnt gap-20">
                <div
                  className="cp reconfigure flex-cnt"
                  onClick={() =>
                    exitConfig?.api.enable && setShowApiModal(true)
                  }
                >
                  <div className="reconfigure-tp">Re-Configure</div>
                  <ReconfigureSVG />
                </div>

                <div className="toggle-btn flex-cnt">
                  <label className="switch">
                    <input
                      type="checkbox"
                      checked={exitConfig?.api.enable}
                      name="custom"
                      onClick={() =>
                        onDataChange(
                          "exitConfig.api.enable",
                          !exitConfig?.api.enable,
                        )
                      }
                    />
                    <span className="toggle round"></span>
                  </label>
                </div>
              </div>
            </div>
          </div>
          <div className="tab-exit">
            <div className="outer-tab pd-20 fx jstfy-sp-btw">
              <div className="left-tab-exit fx gap-20">
                <div className="tab-exit-i flex-cnt">
                  <WebhookSVG />
                </div>
                <div className="tab-exit-c grid gap-8">
                  <div className="fx al-cnt gap-8">
                    <h3>Webhook</h3> <InfoIconSVG />
                  </div>
                  <p>
                    When a particular condition is satisfied on an Webhook
                    Outcome {">"} Setup Webhook Condition{" "}
                  </p>
                </div>
              </div>
              <div className="flex-cnt gap-20">
                <div
                  className="cp reconfigure flex-cnt"
                  onClick={() =>
                    exitConfig?.webhook.enable && setShowWebhookModal(true)
                  }
                >
                  <div className="reconfigure-tp">Re-Configure</div>
                  <ReconfigureSVG />
                </div>

                <div className="toggle-btn flex-cnt">
                  <label className="switch">
                    <input
                      type="checkbox"
                      checked={exitConfig?.webhook.enable}
                      name="custom"
                      onClick={() =>
                        onDataChange(
                          "exitConfig.webhook.enable",
                          !exitConfig?.webhook.enable,
                        )
                      }
                    />
                    <span className="toggle round"></span>
                  </label>
                </div>
              </div>
            </div>
          </div>
          {showApiModal && (
            <ExitCallApiModal
              apiData={exitConfig?.api.apiData ?? apiInit}
              conditionData={
                exitConfig?.api.conditionData.length
                  ? exitConfig.api.conditionData
                  : conditionInit
              }
              nodeVars={nodeVars}
              onDataChange={handleApiValueChange}
              onClose={() => setShowApiModal(false)}
            />
          )}
          {showWebhookModal && (
            <ExitWebhookModal
              webhookData={exitConfig?.api.webhook ?? webhookInit}
              conditionData={
                exitConfig?.webhook.conditionData.length
                  ? exitConfig.webhook.conditionData
                  : conditionInit
              }
              botCustomId={botCustomId}
              nodeVars={nodeVars}
              onDataChange={handleWebhookValueChange}
              onClose={() => setShowWebhookModal(false)}
            />
          )}
        </div>
      </div>
    </motion.div>
  );
}

const rows = Array.from({ length: 6 }, () => ({
  key: "",
  value: "",
  testValue: "",
}));

const apiInit = {
  method: "GET",
  url: "",
  headers: rows,
  params: rows,
  body: rows,
  variables: {},
};

const webhookInit = {
  variables: {},
};

const conditionInit = [
  {
    variable: "",
    value: "",
    comparison_operator: "EQUAL TO",
    logical_operator: "AND",
  },
];

function findVariables(nodes: Array<Node>): Variables {
  const variables: Variables = {};

  for (const node of nodes) {
    if (!node.type) continue;

    const variableData = getVariableData(node.id, node.type, node.data);
    for (const name of variableData.names) {
      const category = variableData.category ?? "node";

      if (!variables[category]) variables[category] = [];
      variables[category]?.push({
        id: `${node.id}-${name}`,
        name,
        type: "string",
        originNode: {
          id: node.id,
          type: node.type,
        },
      });
    }
  }

  return variables;
}

export function responseToVariables(
  response: Record<string, any>,
  exitType: "api" | "webhook",
): Variables {
  const variables: SomeRequired<Variables, "custom"> = { custom: [] };

  for (const key in response) {
    variables.custom.push({
      id: `exit_${exitType}_${key}`,
      name: key,
      type: "string",
      originNode: {
        id: "",
        type: exitType,
      },
    });
  }

  return variables;
}

type DropDownId = "email" | "sms" | "";

const EMAIL_CONDITION = [
  {
    id: "success",
    label: "Email successfully send",
  },
  {
    id: "error",
    label: "Email failed to send",
  },
  {
    id: "opened",
    label: "Email opened by user",
  },
  {
    id: "replied",
    label: "User send reply to the email",
  },
];

const SMS_CONDITION = [
  {
    id: "success",
    label: "SMS successfully send",
  },
  {
    id: "error",
    label: "SMS failed to send",
  },
];

