import { produce } from "immer";

export default function setValueByPath(
  obj: Object,
  path: string,
  value: any
): Object {
  return produce(obj, (draft: any) => {
    const props = path.split(/[.[\]]/g).filter((p) => p);
    const key = props.pop();
    const parent = props.reduce((pre, prop) => pre?.[prop], draft);
    if (!key || !parent) return;
    parent[key] = value;
  });
}
