import AIBotTab_4 from "@/pages/cbb-builder/components/AIBotTab_4";
import Preview from "@/pages/preview/Preview";
import "@/pages/cbb-builder/AiBot.css";
import { motion } from "framer-motion";

const BotDesign = ({ reloadPreview, chatConfig, onDataChange }) => {
  return (
    <>
      <motion.div 
       initial={{opacity:0}}
       animate={{opacity:1}}
       exit={{opacity:0}}
       transition={{duration:1}}
      className="general-container config-bot-container p-fixed ovr-flw-y-hd">
        <div className="embed-flx-bx config-bot-design gap-20">
          <div
            className="embed-main fx fx-d-col al-fx-start fx-strt"
            style={{
              backgroundImage: ` "linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(https://dlvkyia8i4zmz.cloudfront.net/CHBWrT2fTbKG0uliadZa_chatbotbg.png )" : "" }`,
            }}
          >
            <h2 className="mb-15 config-bot-heading">Preview</h2>
            <Preview
              isTrained={true}
              key={reloadPreview}
              chatConfig={chatConfig}
              embed={true}
              environment={"dev"}
            />
            {/* <div className="msg-popup p-absolute">
             <svg xmlns="http://www.w3.org/2000/svg" width="64" height="64" viewBox="0 0 64 64" fill="none">
               <ellipse cx="31.7015" cy="32" rx="31.4386" ry="32" fill="#FB5F66"/>
               <path d="M28.8948 39.0714L33.4211 44L36.8948 38L45.7369 36.9286L44.3685 20L17.9474 23L19.4211 40.0357L28.8948 39.0714Z" fill="white"/>
             </svg>
            </div> */}
          </div>
          <div className="embed-code-box embed__code__box__v2 bot-settings-embed">
            <h2 className="mb-15 config-bot-heading p-sticky">Settings</h2>
            <AIBotTab_4 chatConfig={chatConfig} onDataChange={onDataChange} />
            {/* <div className="flex-cnt n0-mt mb-20">
              <div className="save-btn">save</div>
            </div> */}
          </div>
        </div>
      </motion.div>
    </>
  );
};

export default BotDesign;
