import { Fragment, useEffect, useRef, useState } from "react";
import environment from "../../constants/Environment";
import { BotDesignIcon } from "../../pages/cbb-builder/Builder";
import { TabTitle } from "../../utils/TitleFuntion";
import {
  BotSettingsIcon,
  EditWelcomePageIconSvg,
  EmbedIconSvg,
  ExitConditionSVG,
  IntegrationIconSvg,
  ZapierIconSvg,
} from "@/assets/AiBotAssets";

const BuilderSideNav = ({selectedState,helloBar,botType}) => {
  const [selected, setSelected] = useState<any>(4);
  const [dropdown, setDropdown] = useState<boolean>(false);

  useEffect(() => {
    TabTitle(" | Configure");
    selectedState(selected);
  }, [selected]);

  const dropdownRef = useRef<any>();

  const clickOut = (event: Event) => {
    if (dropdownRef && !dropdownRef.current?.contains(event.target)) {
      setDropdown(false);
    }
  };

  useEffect(() => {
    document.addEventListener("mousedown", clickOut);
    return () => {
      document.removeEventListener("mousedown", clickOut);
    };
  }, []);





  return (
    <Fragment>
      <div className={`config-menu ${(helloBar && environment()?.isAI ) ? "hello-bar-config" : ""}`}>
        <ul className="config-menu-list ">
          <li
            onClick={() => {
              setSelected(4);
            }}
            className={selected === 4 ? "li-active int-drp" : "int-drp "}
          >
            <IntegrationIconSvg />
            <span> Integrations</span>
          </li>

          {environment()?.isAI && (
            <li
              onClick={() => {
                setSelected(5);
              }}
              className={`embed__dropdown ${selected === 5 ? "li-active" : ""}`}
            >
              <EmbedIconSvg />
              <div
                className="fx gap-10 al-cnt"
                onClick={() => setDropdown(!dropdown)}
              >
                <span>Embed on a Webpage</span>
              </div>
            </li>
          )}
          {/* {!environment()?.isAI && (
            <li
              onClick={() => {
                setSelected(9);
              }}
              className={selected === 9 ? "li-active" : ""}
            >
              <BotDesignIcon />
              <span> Bot Design</span>
            </li>
          )} */}

          {/* {!environment()?.isAI && (
            <li
              onClick={() => {
                setSelected(6);
              }}
              className={selected === 6 ? "li-active" : ""}
            >
              <EditWelcomePageIconSvg />
              <span> Edit Welcome page</span>
            </li>
          )} */}
          {environment()?.isAI && (
            <li
              onClick={() => {
                setSelected(7);
              }}
              className={selected === 7 ? "li-active" : ""}
            >
              <BotSettingsIcon />
              <span> Bot Settings</span>
            </li>
          )}
          {botType === "campaign" && (
            <li
              onClick={() => {
                setSelected(10);
              }}
              className={selected === 10 ? "li-active" : ""}
            >
              <ExitConditionSVG />
              <span> Exit Conditions</span>
            </li>
          )}
          <li
            onClick={() => {
              setSelected(8);
            }}
            className={selected === 8 ? "li-active" : ""}
          >
            <ZapierIconSvg />
            <span>Zapier</span>
          </li>
        </ul>
      </div>
    </Fragment>
  );
};

export default BuilderSideNav;
