import { useState, useRef, useMemo, useEffect } from "react";
import {
  AiChatBotShortLogo,
  BrandingLogo,
  TickMark,
  UserAvatar1,
  UserAvatar2,
  UserAvatar3,
  UserAvatar4,
  ChatBuilderIcon,
  LockIcon,
  FreeToggle,
} from "../../../assets/AiBotAssets";
import { ChromePicker } from "react-color";
import environment from "../../../constants/Environment";
import * as filestack from "filestack-js";
import { fontTypes, themeColors, fontSizes } from "../AiBotData";
import { useSelector, useDispatch } from "react-redux";
import { State } from "../../../redux/Types";
import { userSlice } from "../../../redux/User";
import { axiosGet } from "../../../services/axiosService";
import { useNavigate } from "react-router-dom";
const apikey = environment().FILESTACK_API_KEY;
const client = filestack.init(apikey);
interface dropdowns {
  fontType: boolean;
  fontWeight: boolean;
  fontSize: boolean;
}
const dropdownState = {
  fontType: false,
  fontWeight: false,
  fontSize: false,
};
const preMadeUserAvatars = [
  <UserAvatar1 />,
  <UserAvatar2 />,
  <UserAvatar3 />,
  <UserAvatar4 />,
];

const preMadeBotAvatars = [
  "https://dlvkyia8i4zmz.cloudfront.net/EFqzaPhTuSuU22xdqgXg_Layer_1.svg",
  "https://dlvkyia8i4zmz.cloudfront.net/ll8VhXAqSIuthNPmrtQC_Asset_2_1.svg",
  "https://dlvkyia8i4zmz.cloudfront.net/egaBxw0QpewpONdXBhXO_Asset_2_1__1_.svg",
  "https://cdn.filestackcontent.com/6rykG4rRw2W7ccMjRfeJ",
];

function AIBotTab_4({ chatConfig, onDataChange }) {
  const [dropdowns, setDropdowns] = useState<dropdowns>(dropdownState);
  const [rgbColor, setRgbColor] = useState<any>();
  const [fontStyle, setFontStyle] = useState<string>(
    chatConfig?.typography?.font?.displayName,
  );
  const [branding, setBranding] = useState<boolean>(false);
  const [fontStyles, setFontStyles] = useState<any>(fontTypes);
  const [activeFontSizeIndex, setActiveFontSizeIndex] = useState(
    fontSizes?.indexOf(parseInt(chatConfig?.typography?.size)),
  );
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const plan: any = useSelector((state: State) => state.user.plan);
  const dropdownRef = useRef<any>();
  useEffect(() => {
    getCompany();
  }, []);

  async function getCompany() {
    try {
      const response = await axiosGet("/users/getSingleCompany");
      const { company } = response.data.data;
      setBranding(
        environment().isAI
          ? company?.ai_addons?.branding
          : company?.omniaddons?.branding,
      );
    } catch (err) {}
  }
  const uploadCustomIcon = async (type: string = "") => {
    var options = {
      accept: ["image/*", "video/*"],
      fromSources: ["local_file_system", "url", "imagesearch"],
      // imageMin: [25, 25] as [number, number],
      // imageMax: [50, 50] as [number, number],
      transformations: {
        crop: false,
        circle: true,
        rotate: true,
      },
      maxFiles: 1,
      minFiles: 1,
      maxSize: 2097152,
      onFileUploadFinished: (data) => {
        if (type == "logo") onDataChange(`chatConfig.${type}`, data?.url);
        else
          onDataChange(`chatConfig.${type}`, {
            isPreMade: false,
            svgId: 0,
            url: data?.url,
          });
      },
      onFileUploadFailed: (data) => {
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "error",
            message: data?.mimetype + " not supported",
          }),
        );
      },
    };
    const picker = client.picker(options);
    picker.open();
  };

  const handleLogoRemove = async (type: string = "") => {
    onDataChange(`chatConfig.${type}`, "");
  };

  function handleDropdowns(type: string, show: boolean = false) {
    if (show) {
      setDropdowns({ ...dropdownState, [type]: show });
    } else {
      setDropdowns(dropdownState);
    }
  }

  const uploadRules = (
    <div className="upload-instructions mt-5">
      <p className="max-size">Max file size: 5MB </p>
      <p className="upload-avatar-inst">
        Supported formats: JPG, PNG, JPEG |
        <span className="italic-text"> (500px X 500px)</span>
      </p>
    </div>
  );

  const hexToRgb = (hex: string) => {
    let pattern_color = "^#([A-Fa-f0-9]{6})$";

    if (hex.match(pattern_color)) {
      let hex_color = hex.replace("#", "");
      let r = parseInt(hex_color.substring(0, 2), 16);
      let g = parseInt(hex_color.substring(2, 4), 16);
      let b = parseInt(hex_color.substring(4, 6), 16);
      setRgbColor({ r, g, b });
      return { r, g, b };
    }
  };
  const rgbToHex = (rgb: any) => {
    if (rgb) {
      let r =
        rgb.r.toString(16).length === 1
          ? "0" + rgb.r.toString(16)
          : rgb.r.toString(16);
      let g =
        rgb.g.toString(16).length === 1
          ? "0" + rgb.g.toString(16)
          : rgb.g.toString(16);
      let b =
        rgb.b.toString(16).length === 1
          ? "0" + rgb.b.toString(16)
          : rgb.b.toString(16);
      // if(b.length == 1){

      // console.log(`#${r}${g}${b}` + "  hex: ", chatConfig?.color);
      if (chatConfig?.color !== `#${r}${g}${b}`) {
        onDataChange("chatConfig.color", `#${r}${g}${b}`);
      }
    }
  };
  const handleReset = () => {
    let temp = { ...chatConfig, color: "#F1F1F1", theme: "light" };
    onDataChange("chatConfig", temp);
  };

  const handleFontSizeKeyDown = (e) => {
    if (e.key === "ArrowUp") {
      e.preventDefault();
      setActiveFontSizeIndex((prevIndex) => Math.max(prevIndex - 1, 0));
      scrollActiveItemIntoView();
    } else if (e.key === "ArrowDown") {
      e.preventDefault();
      setActiveFontSizeIndex((prevIndex) =>
        Math.min(prevIndex + 1, fontSizes.length - 1),
      );
      scrollActiveItemIntoView();
    } else if (e.key === "Enter") {
      e.preventDefault();
      const selectedSize = fontSizes[activeFontSizeIndex];
      if (selectedSize) {
        onDataChange("chatConfig.typography.size", selectedSize);
        handleDropdowns("fontSize", false);
      }
    }
  };

  const scrollActiveItemIntoView = () => {
    const activeItem = dropdownRef.current.querySelector(".active-item");
    if (activeItem) {
      activeItem.scrollIntoView({
        behavior: "smooth",
        block: "nearest",
        inline: "nearest",
      });
    }
  };

  useEffect(() => {
    if (dropdowns.fontSize) {
      window.addEventListener("keydown", handleFontSizeKeyDown);
    } else {
      window.removeEventListener("keydown", handleFontSizeKeyDown);
    }

    return () => {
      window.removeEventListener("keydown", handleFontSizeKeyDown);
    };
  }, [activeFontSizeIndex, dropdowns.fontSize]);

  useEffect(() => {
    fontTypes.forEach((fontType, index) => {
      let font;
      let id = `dynamic-font${index}`;
      font = document.createElement("link");
      font.rel = "stylesheet";
      font.id = id;
      font.href = fontType?.fontURL;
      document.head.appendChild(font);
    });
  }, []);

  useEffect(() => {
    if (chatConfig?.color.trim().length > 0) {
      hexToRgb(chatConfig?.color);
    }
  }, [chatConfig?.color]);

  useEffect(() => {
    rgbToHex(rgbColor);
  }, [rgbColor]);

  useEffect(() => {
    function handleBlur(event) {
      if (
        dropdownRef.current &&
        !dropdownRef.current.contains(event.relatedTarget)
      ) {
        handleDropdowns("fontType", false);
      }
    }
    document.addEventListener("mousedown", handleBlur);
    return () => {
      document.removeEventListener("mousedown", handleBlur);
    };
  }, [handleDropdowns]);

  useEffect(() => {
    if (fontStyle !== chatConfig?.typography?.font?.displayName)
      setFontStyles(
        fontTypes.filter((fontType) =>
          fontType.displayName.toLowerCase().includes(fontStyle.toLowerCase()),
        ),
      );
    else setFontStyles(fontTypes);
  }, [fontStyle]);
  useEffect(() => {
    if (!dropdowns.fontType) {
      setFontStyle(chatConfig?.typography?.font?.displayName);
      const inputElement = document.querySelector(
        ".dropdown-search-input",
      ) as HTMLInputElement;
      inputElement.blur();
    }
  }, [dropdowns.fontType]);
  useEffect(() => {
    if (
      !dropdowns.fontSize &&
      chatConfig?.typography?.size != fontSizes[activeFontSizeIndex]
    ) {
      setActiveFontSizeIndex(0);
      onDataChange("chatConfig.typography.size", fontSizes[0]);
    }
  }, [dropdowns.fontSize]);

  return (
    <div className="chat-setting-container m-l-r-b-20 bot-design-tab animate-up-d">
      <div className="avatar-setting">
        <h3>Branding Configuration</h3>

        <div className="brand-config">
          <div className="brand-config-left">
            <div className="avatar-parent upload-logo-parent">
              {/* Branding logo for header section */}
              <div className="upload-avatar flex-cnt fx-d-col logo-upload">
                {chatConfig?.logo ? (
                  <div className="img-upload p-relative flex-cnt fx-d-col">
                    <img className="bot-avatar" src={chatConfig?.logo} />
                  </div>
                ) : (
                  <div className="logo-frame">
                    {environment().isAI ? (
                      <ChatBuilderIcon />
                    ) : (
                      <BrandingLogo />
                    )}
                  </div>
                )}

                {!chatConfig?.logo && (
                  <>
                    <label
                      onClick={() => uploadCustomIcon("logo")}
                      className="browse-label upload-logo-heading"
                    >
                      <span className="browse-btn mt-10">Upload Logo</span>
                    </label>
                    {uploadRules}
                  </>
                )}
                {chatConfig?.logo && (
                  <span
                    className="mt-20"
                    onClick={() => handleLogoRemove("logo")}
                  >
                    Remove
                  </span>
                )}
              </div>
            </div>

            {/* Branding logo for header section */}
            <div className="header-profile-config">
              <div className="profile-field">
                <div className="label-heading fx jstfy-sp-btw">
                  <label>Header Name</label>
                  <span>{chatConfig?.headerName.length}/30</span>
                </div>
                <input
                  type="text"
                  placeholder="Type name..."
                  value={chatConfig?.headerName}
                  onChange={(e) =>
                    onDataChange("chatConfig.headerName", e.target.value)
                  }
                  maxLength={30}
                />
              </div>
              <div className="profile-field">
                <label>
                  Description (optional)
                  <div className="toggle-btn">
                    <label className="switch">
                      <input
                        type="checkbox"
                        checked={chatConfig?.showHeaderDesc}
                        onChange={(e) =>
                          onDataChange(
                            "chatConfig.showHeaderDesc",
                            e.target.checked,
                          )
                        }
                      />
                      <span className="toggle round"></span>
                    </label>
                  </div>
                </label>
                <input
                  type="text"
                  placeholder="Type heading name..."
                  value={chatConfig?.headerDescription}
                  onChange={(e) =>
                    onDataChange("chatConfig.headerDescription", e.target.value)
                  }
                  disabled={!chatConfig?.showHeaderDesc}
                  className={`${!chatConfig?.showHeaderDesc && "op-5"}`}
                  maxLength={55}
                />
              </div>
            </div>
          </div>
          <div className="brand-config-right">
            <div className="select-avatar">
              <h4>Choose Bot Avatar</h4>
              <div className="avatar-upload">
                <div className="preset-avatars">
                  {preMadeBotAvatars.map((avatarUrl, index) => {
                    return (
                      <div
                        className={`avatar-frame ${
                          index + 1 === chatConfig?.botAvatar?.svgId
                            ? "avatar-active"
                            : ""
                        }`}
                        onClick={() => {
                          onDataChange("chatConfig.botAvatar", {
                            isPreMade: true,
                            svgId: index + 1,
                            url: chatConfig?.botAvatar?.url,
                          });
                        }}
                        key={index}
                      >
                        {index + 1 === chatConfig?.botAvatar?.svgId && (
                          <img
                            className="tick"
                            src="https://dlvkyia8i4zmz.cloudfront.net/nHed6h3cT2GEXl9XrWyp_check.svg"
                            alt="tick"
                          />
                        )}

                        <img
                          className="avatar-frame-img"
                          src={avatarUrl}
                          alt=""
                        />
                      </div>
                    );
                  })}
                </div>
                <div className="upload-custom-avatar">
                  <div
                    className={`avatar-frame ${
                      !chatConfig?.botAvatar?.isPreMade ? "avatar-active" : ""
                    }`}
                    onClick={() => {
                      if (chatConfig?.botAvatar?.url) {
                        onDataChange("chatConfig.botAvatar", {
                          isPreMade: false,
                          svgId: 0,
                          url: chatConfig?.botAvatar?.url,
                        });
                      } else uploadCustomIcon("botAvatar");
                    }}
                  >
                    <img
                      className={`${
                        chatConfig?.botAvatar?.url.trim()?.length > 0 &&
                        "avatar-frame-img"
                      }`}
                      src={
                        chatConfig?.botAvatar?.url.trim()?.length > 0
                          ? chatConfig?.botAvatar?.url
                          : "https://dlvkyia8i4zmz.cloudfront.net/LCPtGMwTQaaHG5Bf2RSD_Upload_icon.svg"
                      }
                      alt=""
                    />
                    {chatConfig?.botAvatar?.url.trim()?.length > 0 && (
                      <img
                        className="tick"
                        src="https://dlvkyia8i4zmz.cloudfront.net/nHed6h3cT2GEXl9XrWyp_check.svg"
                        alt="tick"
                      />
                    )}

                    <div
                      className="remove-uploaded-avatar"
                      onClick={(e) => {
                        e.stopPropagation();
                        onDataChange("chatConfig.botAvatar", {
                          isPreMade: true,
                          svgId: 1,
                          url: "",
                        });
                      }}
                    >
                      <img
                        src="https://dlvkyia8i4zmz.cloudfront.net/1aMmB1VQKaY6fPnSGOUI_cross.svg"
                        alt=""
                      />
                    </div>
                  </div>
                </div>
              </div>
              {uploadRules}
            </div>
            <div className="select-avatar">
              <h4>Choose User Avatar</h4>
              <div className="avatar-upload">
                <div className="preset-avatars">
                  {preMadeUserAvatars.map((avatar, index) => {
                    return (
                      <div
                        className={`avatar-frame ${
                          index + 1 === chatConfig?.userAvatar?.svgId
                            ? "avatar-active"
                            : ""
                        }`}
                        onClick={() => {
                          onDataChange("chatConfig.userAvatar", {
                            isPreMade: true,
                            svgId: index + 1,
                            url: chatConfig?.userAvatar?.url,
                          });
                        }}
                        key={index}
                      >
                        <div
                          className="avatar-inner"
                          // style={{
                          //   color: index + 1 === chatConfig?.userAvatar?.svgId ? chatConfig?.typography?.color : "#F2ECEC",
                          //   backgroundColor: index + 1 === chatConfig?.userAvatar?.svgId ? chatConfig?.color : "#F2ECEC",
                          // }}
                          style={{ color: "#D1D1D1" }}
                        >
                          {index + 1 === chatConfig?.userAvatar?.svgId && (
                            <img
                              className="tick"
                              src="https://dlvkyia8i4zmz.cloudfront.net/nHed6h3cT2GEXl9XrWyp_check.svg"
                              alt="tick"
                            />
                          )}
                          {avatar}
                        </div>
                      </div>
                    );
                  })}
                  {/* {chatConfig?.userAvatar?.url.trim()?.length  > 0 && ( */}
                  {/* <div
                      className={`avatar-frame? "avatar-active" : ""}`}
                      onClick={() => {
                        onDataChange("chatConfig.userAvatar", { isPreMade: false, svgId: 0, url: chatConfig?.userAvatar?.url });
                      }}
                    >
                      <img src={chatConfig?.userAvatar?.url} alt="" />
                    </div> */}
                  {/* )} */}
                </div>
                <div className="upload-custom-avatar">
                  <div
                    className={`avatar-frame ${
                      !chatConfig?.userAvatar?.isPreMade ? "avatar-active" : ""
                    }`}
                    onClick={() => {
                      if (chatConfig?.userAvatar?.url) {
                        onDataChange("chatConfig.userAvatar", {
                          isPreMade: false,
                          svgId: 0,
                          url: chatConfig?.userAvatar?.url,
                        });
                      } else uploadCustomIcon("userAvatar");
                    }}
                  >
                    <img
                      className={`${
                        chatConfig?.userAvatar?.url?.trim()?.length > 0 &&
                        "avatar-frame-img"
                      }`}
                      src={
                        chatConfig?.userAvatar?.url.trim()?.length > 0
                          ? chatConfig?.userAvatar?.url
                          : "https://dlvkyia8i4zmz.cloudfront.net/LCPtGMwTQaaHG5Bf2RSD_Upload_icon.svg"
                      }
                      alt=""
                    />
                    {!chatConfig?.userAvatar?.isPreMade && (
                      <img
                        className="tick"
                        src="https://dlvkyia8i4zmz.cloudfront.net/nHed6h3cT2GEXl9XrWyp_check.svg"
                        alt="tick"
                      />
                    )}

                    <div
                      className="remove-uploaded-avatar"
                      onClick={(e) => {
                        e.stopPropagation();
                        onDataChange("chatConfig.userAvatar", {
                          isPreMade: true,
                          svgId: 1,
                          url: "",
                        });
                      }}
                    >
                      <img
                        src="https://dlvkyia8i4zmz.cloudfront.net/1aMmB1VQKaY6fPnSGOUI_cross.svg"
                        alt=""
                      />
                    </div>
                  </div>
                </div>
              </div>
              {uploadRules}
            </div>
          </div>
        </div>
      </div>
      <div className="setting-config">
        <div className="branding-setting-config">
          <h3>Branding Setting</h3>
          <div className="branding-parent branding-setting-parent">
            <div className="branding-settings flex-cnt fx-d-col">
              {/* {(!environment().isAI  || (branding && environment().isAI || plan?.name === 'Professional')) && */}

              <div className="branding-toggle fx jstfy-sp-btw padding-10 wd-100 p-relative">
                {((environment().isAI &&
                  plan?.name !== "Professional" &&
                  !branding) ||
                  (!environment().isAI && !branding)) && (
                  <div className="new-config-tooltip fx fx-d-col gap-8">
                    <p>
                      Your plan does not include this feature. Buy{" "}
                      {environment().isAI
                        ? "Professional plan or Add-Ons"
                        : "addons"}{" "}
                      to <span>unlock</span> this feature.
                    </p>
                    {environment().isAI ? (
                      <a
                        onClick={() => {
                          navigate("/settings");
                          dispatch(userSlice.actions.setSettings(2));
                        }}
                      >
                        Upgrade Plan
                      </a>
                    ) : (
                      <a
                        onClick={() => {
                          navigate("/settings");
                          dispatch(userSlice.actions.setSettings(6));
                        }}
                      >
                        Buy Addons
                      </a>
                    )}
                  </div>
                )}
                <div className="branding-toggle-text">
                  {((environment().isAI &&
                    plan?.name !== "Professional" &&
                    !branding) ||
                    (!environment().isAI && !branding)) && <LockIcon />}
                  Use {environment().isAI ? "Chatbot Builder" : "Omniengage"}{" "}
                  Branding
                </div>
                {(environment().isAI &&
                  plan?.name !== "Professional" &&
                  !branding) ||
                (!environment().isAI && !branding) ? (
                  <span className="branding-free-toggle">
                    <FreeToggle />
                  </span>
                ) : (
                  <div className="toggle-btn">
                    <label className="switch">
                      <input
                        type="checkbox"
                        onChange={(e) => {
                          onDataChange(
                            "chatConfig.omniBranding",
                            e.target.checked,
                          );
                        }}
                        checked={chatConfig?.omniBranding}
                      />
                      <span className="toggle round"></span>
                    </label>
                  </div>
                )}
              </div>

              <div
                className={`branding-logo flex-cnt wd-100 ${
                  ((environment().isAI &&
                    !plan?.ai_currentLimits?.aiKey &&
                    !branding) ||
                    (!environment().isAI && !branding)) &&
                  "op-5"
                }`}
              >
                <div className="flex-cnt flex-column g-6">
                  Powered by
                  <p className="branding-text fx al-cnt g-6 cb-logo">
                    <span>
                      {environment().isAI ? (
                        <div>
                          <AiChatBotShortLogo />
                        </div>
                      ) : (
                        <BrandingLogo />
                      )}
                    </span>
                    {environment().isAI ? "Chatbot Builder" : "Omniengage"}{" "}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="font-setting">
          <h3>Font Setting</h3>
          <div className="font-controls">
            <div className="font-controls-inner w-100">
              <div className="ai-input-wrap w-100">
                <label className="mb-8">Font Type</label>
                <div
                  className="botTone-dropdown p-relative"
                  onMouseDown={(e) => e.stopPropagation()}
                  onClick={() => {
                    handleDropdowns("fontType", !dropdowns.fontType);
                    setFontStyle("");
                  }}
                  tabIndex={0}
                >
                  <div className="ai-bot-dropdown fx jstfy-sp-btw al-cnt bg-white">
                    <div
                      className="dropdown-text no-m fx al-cnt dropdown-search wd-100 gap-10"
                      style={{
                        fontFamily: chatConfig?.typography?.font?.fontFamily,
                      }}
                    >
                      <input
                        className="wd-100 bd-none no-pl dropdown-search-input"
                        type="text"
                        value={fontStyle}
                        onChange={(e) => setFontStyle(e.target.value)}
                      />
                    </div>
                    <div className="lh-0">
                      <svg
                        width="10"
                        height="5"
                        viewBox="0 0 10 5"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M0 -0.000488281L5 4.99951L10 -0.000488281H0Z"
                          fill="#6D6D6D"
                        />
                      </svg>
                    </div>
                  </div>
                  {dropdowns.fontType && (
                    <ul
                      ref={dropdownRef}
                      className="dropdown-container bot-tones-container new-scrollbar"
                    >
                      {fontStyles.map((font, idx) => (
                        <li
                          key={idx}
                          className={`fx al-cnt gap-10 ${
                            font?.displayName ===
                            chatConfig?.typography?.font?.displayName
                              ? "active-item"
                              : ""
                          }`}
                          style={{ fontFamily: font?.fontFamily }}
                          onClick={(e) => {
                            onDataChange("chatConfig.typography.font", font);
                            setFontStyle(font.displayName);
                          }}
                        >
                          {font?.displayName}
                        </li>
                      ))}
                      {!fontStyles.length && <li>No Font Style Found.</li>}
                    </ul>
                  )}
                </div>
              </div>
              <div className="ai-input-center w-100">
                <div className="ai-input-wrap w-100">
                  <label>Font Size</label>
                  <div
                    className="botTone-dropdown p-relative"
                    onMouseDown={(e) => e.stopPropagation()}
                    onClick={() =>
                      handleDropdowns("fontSize", !dropdowns.fontSize)
                    }
                  >
                    <div className="ai-bot-dropdown fx jstfy-sp-btw al-cnt bg-white">
                      <div
                        className="dropdown-text no-m fx al-cnt gap-10"
                        style={{ fontFamily: chatConfig?.typography?.font }}
                      >
                        {chatConfig?.typography?.size}px
                      </div>
                      <div className="lh-0">
                        <svg
                          width="10"
                          height="5"
                          viewBox="0 0 10 5"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            d="M0 -0.000488281L5 4.99951L10 -0.000488281H0Z"
                            fill="#6D6D6D"
                          />
                        </svg>
                      </div>
                    </div>
                    {dropdowns.fontSize && (
                      <ul
                        ref={dropdownRef}
                        className="dropdown-container bot-tones-container new-scrollbar"
                      >
                        {fontSizes.map((data, idx) => (
                          <li
                            key={idx}
                            className={`fx al-cnt gap-10 ${
                              idx === activeFontSizeIndex ? "active-item" : ""
                            }`}
                            onClick={() => {
                              onDataChange("chatConfig.typography.size", data);
                              setActiveFontSizeIndex(idx);
                              handleDropdowns("fontSize", false);
                            }}
                            // style={{ fontFamily: chatConfig?.typography?.font.fontFamily}}
                          >
                            {data}px
                          </li>
                        ))}
                      </ul>
                    )}
                  </div>
                </div>
                {/* <div className="ai-input-wrap w-100">
                <label>Font Size</label>
                <div className="font-size-input">
                  <span>Aa</span>
                  <input
                    type="text"
                    value={chatConfig?.typography?.size}
                    onChange={(e) => {
                      // if (e.target.value >= "0" && e.target.value <= "100")
                      onDataChange("chatConfig.typography.size", e.target.value);
                    }}
                  />
                  <p className="font-size-val">px</p>
                </div>
              </div> */}
              </div>
              {/* <div className="ai-input-wrap w-100">
              <label>Font Color</label>
              <div className="font-color-input">
                <span>{chatConfig?.typography?.color}</span>
                <input
                  type="color"
                  value={chatConfig?.typography?.color}
                  onChange={(e) => onDataChange("chatConfig.typography.color", e.target.value)}
                />
              </div>
            </div> */}
            </div>
          </div>
        </div>
      </div>

      <div className="color-theme mt-25">
        <h3>color theme</h3>
        <div className="url-crawl fx jstfy-sp-btw color-theme-control gap-40">
          <div className="fx fx-d-col color-container w-50">
            <div className="select-themes">
              <p className="select-color-heading fx jstfy-sp-btw al-cnt flex-start mb-8">
                Color theme{" "}
              </p>

              <div className="theme-change">
                <div className="theme-change-button">
                  <button
                    className={
                      chatConfig?.theme === "light" ? "aibot-active" : ""
                    }
                    onClick={() => onDataChange("chatConfig.theme", "light")}
                  >
                    <img
                      src="https://dlvkyia8i4zmz.cloudfront.net/J6riZIeFSsKzjWfzKhHh_Group_14406.svg"
                      alt=""
                    />
                    <img
                      className="tick"
                      src="https://dlvkyia8i4zmz.cloudfront.net/nHed6h3cT2GEXl9XrWyp_check.svg"
                      alt="tick"
                    />
                  </button>
                  <p>Light</p>
                </div>
                <div className="theme-change-button">
                  <button
                    className={
                      chatConfig?.theme === "dark" ? "aibot-active" : ""
                    }
                    onClick={() => onDataChange("chatConfig.theme", "dark")}
                  >
                    <img
                      src="https://dlvkyia8i4zmz.cloudfront.net/1bVwFvj2QbIa7ZwaqNzZ_Group_14406__1_.svg"
                      alt=""
                    />
                    <img
                      className="tick"
                      src="https://dlvkyia8i4zmz.cloudfront.net/nHed6h3cT2GEXl9XrWyp_check.svg"
                      alt="tick"
                    />
                  </button>
                  <p>Dark</p>
                </div>
              </div>

              <p className="choose-color-title">Choose Accent Color</p>
              <div className="colors mt-8">
                {themeColors.map((clr, index) => {
                  return (
                    <div
                      className="color flex-cnt color-circle"
                      key={clr.id}
                      style={{ background: clr.dark }}
                      onClick={() => onDataChange("chatConfig.color", clr.dark)}
                    >
                      {chatConfig?.color === clr.dark.toLowerCase() ? (
                        <TickMark />
                      ) : (
                        ""
                      )}
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
          {/* <div className="or-line">
            <svg width="19" height="205" viewBox="0 0 19 205" fill="none" xmlns="http://www.w3.org/2000/svg">
              <rect x="8.96484" y="0.586914" width="1" height="82" fill="#CFCFCF" />
              <path
                d="M4.85884 106.717C4.12218 106.717 3.48951 106.591 2.96084 106.34C2.43218 106.08 2.02484 105.686 1.73884 105.157C1.45284 104.62 1.29251 103.948 1.25784 103.142C1.24918 102.761 1.24484 102.397 1.24484 102.05C1.24484 101.695 1.24918 101.326 1.25784 100.945C1.29251 100.148 1.45718 99.4846 1.75184 98.9559C2.05518 98.4186 2.47118 98.0199 2.99984 97.7599C3.53718 97.4912 4.15684 97.3569 4.85884 97.3569C5.56951 97.3569 6.18918 97.4912 6.71784 97.7599C7.25518 98.0199 7.67551 98.4186 7.97884 98.9559C8.28218 99.4846 8.44684 100.148 8.47284 100.945C8.49018 101.326 8.49884 101.695 8.49884 102.05C8.49884 102.397 8.49018 102.761 8.47284 103.142C8.44684 103.948 8.28651 104.62 7.99184 105.157C7.70584 105.686 7.29851 106.08 6.76984 106.34C6.24118 106.591 5.60418 106.717 4.85884 106.717ZM4.85884 105.612C5.52618 105.612 6.06784 105.413 6.48384 105.014C6.90851 104.615 7.13818 103.97 7.17284 103.077C7.19018 102.687 7.19884 102.34 7.19884 102.037C7.19884 101.725 7.19018 101.378 7.17284 100.997C7.15551 100.399 7.04284 99.9136 6.83484 99.5409C6.63551 99.1682 6.36684 98.8952 6.02884 98.7219C5.69084 98.5486 5.30084 98.4619 4.85884 98.4619C4.43418 98.4619 4.05284 98.5486 3.71484 98.7219C3.37684 98.8952 3.10384 99.1682 2.89584 99.5409C2.69651 99.9136 2.58384 100.399 2.55784 100.997C2.54918 101.378 2.54484 101.725 2.54484 102.037C2.54484 102.34 2.54918 102.687 2.55784 103.077C2.59251 103.97 2.82218 104.615 3.24684 105.014C3.67151 105.413 4.20884 105.612 4.85884 105.612ZM10.6927 106.587C10.5974 106.587 10.5237 106.561 10.4717 106.509C10.4197 106.448 10.3937 106.375 10.3937 106.288V97.7989C10.3937 97.7036 10.4197 97.6299 10.4717 97.5779C10.5237 97.5172 10.5974 97.4869 10.6927 97.4869H13.8647C14.844 97.4869 15.6197 97.7166 16.1917 98.1759C16.7637 98.6352 17.0497 99.3156 17.0497 100.217C17.0497 100.884 16.8807 101.43 16.5427 101.855C16.2134 102.271 15.7627 102.561 15.1907 102.726L17.1927 106.184C17.2187 106.236 17.2317 106.284 17.2317 106.327C17.2317 106.396 17.2014 106.457 17.1407 106.509C17.0887 106.561 17.028 106.587 16.9587 106.587H16.3347C16.1874 106.587 16.079 106.548 16.0097 106.47C15.9404 106.392 15.8797 106.314 15.8277 106.236L13.9427 102.934H11.6677V106.288C11.6677 106.375 11.6374 106.448 11.5767 106.509C11.5247 106.561 11.451 106.587 11.3557 106.587H10.6927ZM11.6677 101.829H13.8127C14.4627 101.829 14.948 101.695 15.2687 101.426C15.5894 101.149 15.7497 100.741 15.7497 100.204C15.7497 99.6752 15.5894 99.2722 15.2687 98.9949C14.9567 98.7176 14.4714 98.5789 13.8127 98.5789H11.6677V101.829Z"
                fill="#444444"
              />
              <rect x="8.96484" y="122.587" width="1" height="82" fill="#CFCFCF" />
            </svg>
          </div> */}
          <div className="custom-color-picker w-50">
            <div className="select-themes">
              <p className="select-color-heading mb-8">
                Select a custom color{" "}
              </p>
              <ChromePicker
                color={chatConfig?.color}
                onChange={(color) => {
                  onDataChange("chatConfig.color", color.hex);
                }}
              />
              {/* <HexColorPicker 
               color={chatConfig?.color} 
               onChange={(color) => {
                console.log(color);
                console.log(chatConfig?.color);
                  onDataChange("chatConfig.color", color.toLowerCase());
                }} 
                /> */}
              <div className="color-codes">
                <div className="unique-code">
                  <h5 className="unique-code-name">RGB</h5>
                  {/* <input type="text" value={chatConfig?.color} onChange={(e) => onDataChange("chatConfig.color", e.target.value)} /> */}

                  <div className="rgb_values  fx jstfy-cnt p-relative gap-8">
                    <input
                      type="number"
                      min="0"
                      max="255"
                      value={rgbColor?.r ?? 0}
                      onChange={(e) =>
                        setRgbColor({ ...rgbColor, r: Number(e.target.value) })
                      }
                    />
                    <input
                      type="number"
                      min="0"
                      max="255"
                      value={rgbColor?.g ?? 0}
                      onChange={(e) =>
                        setRgbColor({ ...rgbColor, g: Number(e.target.value) })
                      }
                    />
                    <input
                      type="number"
                      min="0"
                      max="255"
                      value={rgbColor?.b ?? 0}
                      onChange={(e) =>
                        setRgbColor({ ...rgbColor, b: Number(e.target.value) })
                      }
                    />
                  </div>
                </div>
                <div className="unique-code">
                  <h5 className="unique-code-name">HEX</h5>
                  <input
                    type="text"
                    className="hex_value"
                    value={chatConfig?.color}
                    onChange={(e) =>
                      onDataChange("chatConfig.color", e.target.value)
                    }
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
export default AIBotTab_4;
